import React from "react";
import classes from "./CloseIcon.module.scss";
import { RED } from "../../../data/colors";

interface IProps {
  className?: string;
  style?: React.CSSProperties;
  color?: string;
}

export const CloseIcon = ({ className = "", style, color = RED }: IProps) => (
  <div className={`${classes.closeIcon} ${className}`} style={style}>
    <div className={classes.linesWrapper}>
      <div style={{ backgroundColor: color }} />
      <div style={{ backgroundColor: color }} />
    </div>
  </div>
);
