import {
  combineReducers,
  configureStore,
  ThunkAction,
  Action,
  AnyAction,
  Reducer,
} from "@reduxjs/toolkit";
import current from "./current/reducer";
import user from "./user/reducer";
import company from "./company/reducer";
import searches from "./searches/reducer";
import brands from "./brands/reducer";
import widgets from "./widgets/reducer";
import campaignsSlice from "../features/campaigns/campaignsSlice";
import subscriptionPlansSlice from "../features/subscriptionPlans/subscriptionPlansSlice";
import faqsSlice from "../features/faqs/faqsSlice";
import marketsSlice from "../features/markets/marketsSlice";
import industriesSlice from "../features/industries/industriesSlice";
import dashboardsSlice from "../features/dashboards/dashboardsSlice";

const appReducer = combineReducers({
  current,
  user,
  company,
  searches,
  brands,
  widgets,
  industries: industriesSlice,
  markets: marketsSlice,
  campaigns: campaignsSlice,
  subscriptionPlans: subscriptionPlansSlice,
  faqs: faqsSlice,
  dashboards: dashboardsSlice,
});

const rootReducer: Reducer = (state: RootState, action: AnyAction) => {
  if (action.type === "user/loggedOut") {
    state = {} as RootState;
  }
  return appReducer(state, action);
};

export const store = configureStore({
  reducer: rootReducer,
});

export type RootState = ReturnType<typeof appReducer>;
export type AppThunk<ReturnType = void> = ThunkAction<
  ReturnType,
  RootState,
  unknown,
  Action<string>
>;
export type AppDispatch = typeof store.dispatch;
