import React from "react";
import "./SVG.scss";
import {
  MarketometerIcon,
  DataRobustnessScoreIcon,
  EffectivenessMeasurementIcon,
  ShareOfEarnedMediaIcon,
  ShareOfSearchIcon,
  DashboardIcon,
  SummaryIcon,
  CompetitivenessIcon,
  AttitudeIcon,
  BusinessDriversIcon,
  KeyAreasIcon,
  SentimentDriversIcon,
  GeneralAttributesIcon,
  PositionVsCompetitionIcon,
  AwarenessIcon,
  ConsiderationIcon,
  PreferenceIcon,
  PurchaseIntentIcon,
  ReputationIcon,
  TrackPositionIcon,
  FunnelIcon,
  FunnelOverviewIcon,
  LockIcon,
  TimelineIcon,
  PieIcon,
  CogwheelIcon,
  MaleIcon,
  FemaleIcon,
  TrashCanIcon,
  WebsiteLogoIcon1,
  DownloadIcon,
  CellIcon,
  DocumentIcon,
  InfoIcon,
  BarIcon,
  EditIcon,
  CampaignIcon,
  WarningIcon,
} from "./SVGs";

interface IProps {
  name: SvgName;
  className?: string;
  color?: string;
  width?: number;
  height?: number;
  clickHandler?: () => void;
}

export const SVG = ({ name, className = "", color, width, height, clickHandler = () => {} }: IProps) => {
  const icons = {
    marketometer: <MarketometerIcon color={color} />,
    dataRobustnessScore: <DataRobustnessScoreIcon color={color} />,
    effectivenessMeasurement: <EffectivenessMeasurementIcon color={color} />,
    shareOfEarnedMedia: <ShareOfEarnedMediaIcon color={color} />,
    shareOfSearch: <ShareOfSearchIcon color={color} />,
    dashboard: <DashboardIcon color={color} />,
    summary: <SummaryIcon color={color} />,
    competitiveness: <CompetitivenessIcon color={color} />,
    attitude: <AttitudeIcon color={color} />,
    businessDrivers: <BusinessDriversIcon color={color} />,
    keyAreas: <KeyAreasIcon color={color} />,
    sentimentDrivers: <SentimentDriversIcon color={color} />,
    generalAttributes: <GeneralAttributesIcon color={color} />,
    positionVsCompetition: <PositionVsCompetitionIcon color={color} />,
    awareness: <AwarenessIcon color={color} />,
    consideration: <ConsiderationIcon color={color} />,
    preference: <PreferenceIcon color={color} />,
    purchaseIntent: <PurchaseIntentIcon color={color} />,
    reputation: <ReputationIcon color={color} />,
    trackPosition: <TrackPositionIcon color={color} />,
    funnel: <FunnelIcon color={color} />,
    funnelOverview: <FunnelOverviewIcon color={color} />,
    lock: <LockIcon color={color} />,
    timeline: <TimelineIcon color={color} />,
    pie: <PieIcon color={color} />,
    cogwheel: <CogwheelIcon color={color} />,
    male: <MaleIcon className={className} clickHandler={clickHandler} />,
    female: <FemaleIcon className={className} clickHandler={clickHandler} />,
    trashCan: <TrashCanIcon className={className} color={color} width={width} height={height} />,
    websiteLogoIcon1: <WebsiteLogoIcon1 className={className} color={color} width={width} height={height} clickHandler={clickHandler} />,
    download: <DownloadIcon color={color} />,
    cell: <CellIcon color={color} />,
    document: <DocumentIcon color={color} />,
    info: <InfoIcon color={color} clickHandler={clickHandler} />,
    bar: <BarIcon color={color} />,
    edit: <EditIcon className={className} color={color} clickHandler={clickHandler} />,
    campaign: <CampaignIcon className={className} color={color} width={width} height={height} clickHandler={clickHandler} />,
    warning: <WarningIcon width={width} height={height} />,
  };

  return icons[name] || null;
};
