import React, { CSSProperties } from "react";
import classes from "./DownloadImageButton.module.scss";
import { SVG } from "../../index";

interface Props {
  className?: string;
  style?: CSSProperties;
  clickHandler?: () => void;
}

export const DownloadImageButton = ({ className = "", style, clickHandler }: Props) => {
  return (
    <button className={`${classes.downloadImageButton} ${className}`} style={style} onClick={clickHandler}>
      <SVG name="download" /> <span>Download Image</span>
    </button>
  );
};
