import actionTypes from "../actionTypes";
import { postCompanySubscriptionPlanUpgrade, deleteUser } from "../api";
import firebase from "firebase/app";
import "firebase/auth";
import "firebase/firestore";
import { setSearches } from "../actions";

// MUTATIONS
export const setCompanyData = (companyData: Partial<Store.Company>) => {
  return {
    type: actionTypes.SET_COMPANY_DATA,
    companyData,
  };
};

export const removeCompanyData = () => {
  return {
    type: actionTypes.REMOVE_COMPANY_DATA,
  };
};

// ACTIONS
export const readCompany = (companyId: string): Function => {
  return async (dispatch: Function, getState: Function) => {
    const db = firebase.firestore();
    const docRef = db.collection("companies").doc(companyId);
    const doc = await docRef.get();
    if (doc.exists) {
      const data = doc.data();
      if (data) {
        const enterpriseDashboards: Store.Company["enterpriseDashboards"] = [];
        if (Array.isArray(data.enterpriseDashboards)) {
          data.enterpriseDashboards.forEach(({ id, name }) => {
            if (typeof id === "string" && typeof name === "string") {
              enterpriseDashboards.push({
                id,
                name,
              });
            }
          });
        }

        const members: Store.Company["members"] = [];
        if (typeof data.team === "object") {
          const { user } = getState() as Store.State;

          for (const memberArr of Object.entries(data.team)) {
            const [id, member]: [string, any] = memberArr;
            let email = "";

            if (id === user.id) {
              email = user.email;
            } else {
              const doc = await db.collection("userProfiles").doc(id).get();

              if (doc.exists) {
                const data = doc.data();
                email = data?.email || "";
              }
            }

            members.push({
              id: String(id),
              fullName: String(member.fullName ?? ""),
              email,
              isAdmin: member.role === "admin",
            });
          }
        }

        dispatch(
          setCompanyData({
            id: companyId,
            name: String(data.name),
            subscriptionPlanIds: {
              current: String(data.subscriptionPlan),
              requested: data.requstedSubscriptionPlanId
                ? String(data.requstedSubscriptionPlanId)
                : undefined,
            },
            enterpriseDashboards,
            members,
          })
        );
      }
    }
  };
};

export const updateCompanyData = (
  payload: Partial<{
    name: string;
  }>
): Function => {
  return async (dispatch: Function, getState: Function) => {
    const { company } = getState() as Store.State;
    const db = firebase.firestore();
    const docRef = db.collection("companies").doc(company.id);
    await docRef.set(payload, { merge: true });
    dispatch(setCompanyData(payload));
  };
};

export const requestCompanySubscriptionPlanUpgrade = (
  requestedSubscriptionPlanId: string
): Function => {
  return async (dispatch: Function, getState: Function) => {
    const { user, company } = getState() as Store.State;
    await postCompanySubscriptionPlanUpgrade(
      user.id,
      requestedSubscriptionPlanId
    );
    dispatch(
      setCompanyData({
        subscriptionPlanIds: {
          ...company.subscriptionPlanIds,
          requested: requestedSubscriptionPlanId,
        },
      })
    );
  };
};

export const toggleCompanyMemberStatus = (
  member: Store.Company["members"][number]
): Function => {
  return async (dispatch: Function, getState: Function) => {
    const { company } = getState() as Store.State;
    const db = firebase.firestore();
    await db
      .collection("companies")
      .doc(company.id)
      .update({
        [`team.${member.id}.role`]: member.isAdmin ? "employee" : "admin",
      });
    dispatch(
      setCompanyData({
        members: company.members.map((mem) => ({
          ...mem,
          isAdmin: mem.id === member.id ? !mem.isAdmin : mem.isAdmin,
        })),
      })
    );
  };
};

export const deleteCompanyMember = (
  memberSearchIds: string[],
  memberId: string
): Function => {
  return async (dispatch: Function, getState: Function) => {
    const { user, company, searches } = getState() as Store.State;
    const db = firebase.firestore();

    const userProfilesCollection = db.collection("userProfiles");

    if (memberSearchIds.length) {
      const userSearchIds = searches
        .filter(({ owner }) => owner.id === user.id)
        .map(({ id }) => id);
      await Promise.all([
        userProfilesCollection.doc(user.id).update({
          brands: [...userSearchIds, ...memberSearchIds],
        }),
        userProfilesCollection.doc(memberId).update({
          brands: [],
        }),
      ]);
      dispatch(
        setSearches(
          searches.map((search) => ({
            ...search,
            owner: {
              id: search.owner.id === memberId ? user.id : search.owner.id,
            },
          }))
        )
      );
    }

    await Promise.all([
      deleteUser(memberId),
      userProfilesCollection.doc(memberId).delete(),
    ]);

    await db
      .collection("companies")
      .doc(company.id)
      .update({
        [`team.${memberId}`]: firebase.firestore.FieldValue.delete(),
      });
    dispatch(
      setCompanyData({
        members: company.members.filter(({ id }) => id !== memberId),
      })
    );
  };
};
