// May 1, 20
export const formatDate = (dateString: string | number | Date) => {
  return new Date(dateString).toLocaleDateString("en", { day: "numeric", month: "short", year: "2-digit" });
};

// 2020-05-01
export const formatDate2 = (date: string | number | Date) => {
  if (typeof date === "string" && !Date.parse(date)) return "";

  const _date = new Date(date);

  const year = _date.getFullYear();
  const month = _date.getMonth() + 1;
  const day = _date.getDate();

  return `${year}-${("0" + month).slice(-2)}-${("0" + day).slice(-2)}`;
};

export function getDateWithMonthOffset(monthOffset: number) {
  return new Date(new Date().setMonth(new Date().getMonth() + monthOffset));
}
