import React, { useState, useEffect, useRef, CSSProperties } from "react";
import classes from "./VerticalFunnelChart.module.scss";
import { BLUE, RED, VIOLET, GOLD } from "../../../data/colors";

const colors = [BLUE, RED, VIOLET, GOLD];

interface Props {
  className?: string;
  style?: CSSProperties;
  data?: {
    label: string;
    value: number;
  }[];
}

export const VerticalFunnelChart = ({ className, style, data = [] }: Props) => {
  const [blockSize, setBlockSize] = useState({
    width: 0,
    height: 0,
  });

  const row = useRef<HTMLDivElement>(null);

  useEffect(() => {
    _setBlockSize();
  }, [data]);

  useEffect(() => {
    window.addEventListener("resize", _setBlockSize);

    return () => {
      window.removeEventListener("resize", _setBlockSize);
    };
  }, []);

  function _setBlockSize() {
    setBlockSize({
      width: row.current?.offsetWidth || 0,
      height: row.current?.offsetHeight || 0,
    });
  }

  function calcShapeBorderWidth(value: number, nextValue: number): string {
    return (((value - nextValue) / 100 / 2 / 2) * blockSize.width).toFixed();
  }

  const shapeBorderHeight = (blockSize.height / 2).toFixed();

  // function calcConversionRate(value1: number, value2: number): string {
  //   return ((value1 / value2) * 100).toFixed();
  // }

  const isDataValid = data.length > 1;

  return isDataValid ? (
    <div className={`${classes.verticalFunnelChart} ${className}`} style={style}>
      {data.slice(0, 4).map(({ label, value }, i, arr) => (
        <div className={classes.rowWrapper} key={i}>
          <div ref={row} className={classes.row}>
            <div className={classes.shapeWrapper}>
              {i < data.length - 1 ? (
                <>
                  <div
                    style={{
                      borderLeft: calcShapeBorderWidth(i === 0 ? 100 : value, arr[i + 1]!.value) + "px solid transparent",
                      borderRight: calcShapeBorderWidth(i === 0 ? 100 : value, arr[i + 1]!.value) + "px solid " + colors[i],
                      borderTop: shapeBorderHeight + "px solid " + colors[i],
                      borderBottom: shapeBorderHeight + "px solid transparent",
                    }}
                  />
                  <div style={{ width: arr[i + 1]!.value + "%", backgroundColor: colors[i] }} />
                  <div
                    style={{
                      borderLeft: calcShapeBorderWidth(i === 0 ? 100 : value, arr[i + 1]!.value) + "px solid " + colors[i],
                      borderRight: calcShapeBorderWidth(i === 0 ? 100 : value, arr[i + 1]!.value) + "px solid transparent",
                      borderTop: shapeBorderHeight + "px solid " + colors[i],
                      borderBottom: shapeBorderHeight + "px solid transparent",
                    }}
                  />
                </>
              ) : (
                <div style={{ width: value + "%", backgroundColor: colors[i] }} />
              )}
            </div>
            <div className={classes.labelWrapper} title={value + "% " + label.toUpperCase()}>
              <div style={{ backgroundColor: colors[i] }}>{value}%</div>
              <div>{label}</div>
            </div>
            {/* {i > 0 && (
              <div className={classes.rateWrapper} title={calcConversionRate(value, arr[i - 1].value) + "% CONVERSION RATE"}>
                <span>{calcConversionRate(value, arr[i - 1].value)}%</span> <span>CONVERSION RATE</span>
              </div>
            )} */}
            {/* {i < data.length - 1 && <div className={classes.pointer} style={{ borderTopColor: colors[i], borderRightColor: colors[i] }} />} */}
          </div>
        </div>
      ))}
    </div>
  ) : null;
};
