import React, { useState, useEffect, useRef, CSSProperties, useMemo } from "react";
import styles from "./HorizontalFunnelChart.module.scss";
import { BLUE, RED, VIOLET, GOLD } from "../../../data/colors";

const colors = [BLUE, RED, VIOLET, GOLD];

interface Props {
  className?: string;
  style?: CSSProperties;
  data?: {
    label: string;
    value: number;
    description?: string;
  }[];
}

export const HorizontalFunnelChart = ({ className = "", style, data: initialData = [] }: Props) => {
  //DATA
  const data = useMemo(() => initialData.slice(0, 4), [initialData]);

  //BLOCK SIZE
  const [blockSize, setBlockSize] = useState({
    width: 0,
    height: 0,
  });

  const block = useRef<HTMLDivElement>(null);

  useEffect(() => {
    _setBlockSize();
  }, [data.length]);

  useEffect(() => {
    window.addEventListener("resize", _setBlockSize);

    return () => {
      window.removeEventListener("resize", _setBlockSize);
    };
  }, []);

  function _setBlockSize() {
    setBlockSize({
      width: block.current?.offsetWidth || 0,
      height: block.current?.offsetHeight || 0,
    });
  }

  const shapeBorderWidth = (blockSize.width / 2).toFixed();

  function calcShapeBorderHeight(value: number, nextValue: number): string {
    return (((value - nextValue) / 100 / 2 / 2) * blockSize.height).toFixed();
  }

  // TICKS
  const ticks = useMemo(() => {
    const interval = blockSize.height > 320 ? 10 : 25;

    return [...Array(200 / interval + 1).keys()].map((key) => Math.abs(100 - interval * key));
  }, [blockSize.height]);

  const Ticks = (style?: CSSProperties) => (
    <div className={styles.ticks} style={style}>
      {ticks.map((tick, i) => (
        <div key={i}>{tick}%</div>
      ))}
    </div>
  );

  const TicksGrid = () => (
    <div className={styles.ticksGrid}>
      {ticks.map((_, i) => (
        <div key={i} />
      ))}
    </div>
  );

  return data.length ? (
    <div className={`${styles.horizontalFunnelChart} ${className}`} style={style}>
      <div className={styles.main}>
        {Ticks()}
        <div className={styles.blocksWrapper}>
          {TicksGrid()}
          <div className={styles.blocks}>
            {data.map(({ label, value }, i, arr) => (
              <div className={styles.blockWrapper} key={i}>
                <div ref={block} className={styles.block}>
                  <div className={styles.shapeWrapper}>
                    {i < arr.length - 1 ? (
                      <>
                        <div
                          style={{
                            borderLeft: shapeBorderWidth + "px solid " + colors[i],
                            borderRight: shapeBorderWidth + "px solid transparent",
                            borderTop: calcShapeBorderHeight(i === 0 ? 100 : value, arr[i + 1]!.value) + "px solid transparent",
                            borderBottom: calcShapeBorderHeight(i === 0 ? 100 : value, arr[i + 1]!.value) + "px solid " + colors[i],
                          }}
                        />
                        <div style={{ height: arr[i + 1]!.value + "%", backgroundColor: colors[i] }} />
                        <div
                          style={{
                            borderLeft: shapeBorderWidth + "px solid " + colors[i],
                            borderRight: shapeBorderWidth + "px solid transparent",
                            borderTop: calcShapeBorderHeight(i === 0 ? 100 : value, arr[i + 1]!.value) + "px solid " + colors[i],
                            borderBottom: calcShapeBorderHeight(i === 0 ? 100 : value, arr[i + 1]!.value) + "px solid transparent",
                          }}
                        />
                      </>
                    ) : (
                      <div style={{ height: value + "%", backgroundColor: colors[i] }} />
                    )}
                  </div>
                  <div className={styles.label}>
                    <div title={label}>{label}</div>
                    <div style={{ borderColor: colors[i] }} />
                  </div>
                  <div className={styles.value}>{value}%</div>
                </div>
              </div>
            ))}
          </div>
        </div>
        {Ticks({ alignItems: "flex-start" })}
      </div>
      <div className={styles.descriptions}>
        {data.map(({ description }, i) => (
          <div key={i}>{description}</div>
        ))}
      </div>
    </div>
  ) : null;
};
