import { FC, memo } from "react";
import styles from "./Header.module.scss";
import { Link } from "react-router-dom";
import { WebsiteLogoHorizontal, Sun, Moon, Profile } from "../assets/icons";
import { useSelector } from "react-redux";

interface Props {
  isDarkTheme: boolean;
  onThemeIconClicked: (isDarkTheme: boolean) => void;
}

export const Header: FC<Props> = memo(({ isDarkTheme, onThemeIconClicked }) => {
  const isAuthenticated = useSelector(({ user }: Store.State) => !!user.id);

  return (
    <div className={styles.header}>
      <Link to="/home" className={styles.logoLink}>
        <WebsiteLogoHorizontal className={styles.logoIcon} />
      </Link>
      <div className={styles.rightSide}>
        {isDarkTheme ? (
          <Sun
            className={styles.themeIcon}
            size={20}
            onClick={() => onThemeIconClicked(false)}
          />
        ) : (
          <Moon
            className={styles.themeIcon}
            size={20}
            onClick={() => onThemeIconClicked(true)}
          />
        )}
        {isAuthenticated && (
          <Link to="/profile" className={styles.profileLink}>
            <Profile className={styles.profileIcon} />
          </Link>
        )}
      </div>
    </div>
  );
});
