import firestore from "../../firestore";
import axios from "../../axios";
import { compareTwoStrings } from "string-similarity";
import { getSlugifiedString } from "../../utils";
import yougovBrands from "../../data/yougovBrands";

const urlInsertion = process.env.REACT_APP_ENV;

export const getSearches = (companyId: string) =>
  axios.get(`companies-${urlInsertion}-api/${companyId}/searches`);

export const postSearch = (payload: Parameters<typeof prepareSearchData>[0]) =>
  axios.post(
    `brands-${urlInsertion}-api/searches/new`,
    prepareSearchData(payload)
  );

export const putSearch = (
  searchId: string,
  payload: Parameters<typeof prepareSearchData>[0]
) => {
  return axios.put(
    `brands-${urlInsertion}-api/${searchId}`,
    prepareSearchData(payload)
  );
};

export const deleteSearch = (userId: string, searchId: string) =>
  axios.delete(`brands-${urlInsertion}-api/${searchId}/${userId}`);

export const fetchKeywordSuggestions = async (
  brandName: string,
  marketCode: string
) => {
  const slugifiedBrandName = getSlugifiedString(brandName);

  type Response = {
    [slugifiedBrandName: string]: {
      string: string;
      volume: number;
    }[];
  };

  const { data }: { data: Response } = await axios.post(
    `alice-${urlInsertion}-api/populate-keywords`,
    {
      companies: [
        {
          tableName: slugifiedBrandName,
          name: brandName,
          market: marketCode,
        },
      ],
    }
  );

  const uniqueSuggestions: {
    [id: string]: {
      volume: number;
      withoutBrand?: string;
    };
  } = {};

  brandName = brandName.toLowerCase();

  data[slugifiedBrandName]?.forEach(({ string, volume }) => {
    if (uniqueSuggestions[string]) return;

    const splittedString = string.split(" ");

    let bestMatch = {
      similarityScore: 0,
      endIndex: 0,
    };

    for (let i = 1; i <= splittedString.length; i++) {
      const substring = splittedString.slice(0, i).join(" ");
      const similarityScore = compareTwoStrings(substring, brandName);

      if (similarityScore > bestMatch.similarityScore) {
        bestMatch = {
          similarityScore,
          endIndex: i,
        };
      }
    }

    const { similarityScore, endIndex } = bestMatch;

    const isValidWithoutBrand =
      similarityScore >= 0.75 && endIndex !== splittedString.length;

    uniqueSuggestions[string] = {
      volume,
      ...(isValidWithoutBrand && {
        withoutBrand: splittedString.slice(endIndex).join(" "),
      }),
    };
  });

  return Object.entries(uniqueSuggestions)
    .map(([id, values]) => ({ id, ...values }))
    .sort((a, b) => b.volume - a.volume);
};

export const fetchMentions = async (brandName: string, marketCode: string) => {
  const slugifiedBrandName = getSlugifiedString(brandName);

  const { data } = await axios.post(
    `robustness-score-widget-${urlInsertion}-api/articles`,
    {
      name: `${slugifiedBrandName}_${marketCode}`,
    }
  );

  if (Array.isArray(data)) {
    return data.reduce<
      {
        id: string;
        articleTitle: string;
        link: string;
        source: string;
        category: string;
        time: number;
      }[]
    >((acc, { id, article, source, category, date }) => {
      if (
        typeof id === "string" &&
        typeof article?.title === "string" &&
        typeof article?.url === "string" &&
        typeof source === "string" &&
        typeof category === "string" &&
        typeof date === "string"
      ) {
        acc.push({
          id,
          articleTitle: article.title,
          link: `<a href='${article.url}' target='_blank'>${article.title}</a>`,
          source,
          category,
          time: Date.parse(date),
        });
      }

      return acc;
    }, []);
  }
};

export async function writeSearchExcludedSuggestions(
  searchId: string,
  brandId: string,
  excludedSuggestions: string[]
) {
  await firestore()
    .collection("brands")
    .doc(searchId)
    .update({
      [`meltwater.${brandId}.exclude`]: excludedSuggestions,
    });
}

export async function postSearchRecalculationRequest(searchId: string) {
  return axios.post(`alice-${urlInsertion}-api/recalculate-widgets`, {
    brandId: searchId,
  });
}

export function watchSearch(
  searchId: string,
  callback: (doc: firestore.DocumentData | undefined) => void
) {
  const doc = firestore().collection("brands").doc(searchId);

  return doc.onSnapshot(
    (doc) => {
      callback(doc.data());
    },
    (error) => {
      console.dir(error);
    }
  );
}

function prepareSearchData({
  userId,
  name,
  mainBrand,
  competitors,
  industryId,
  industry,
  marketCode,
  suggestions,
}: {
  userId: string;
  name: string;
  mainBrand: string;
  competitors: string[];
  industryId: string;
  industry?: ValueOf<RootState["industries"]["entities"]>;
  marketCode: string;
  suggestions: Store.Search["suggestions"];
}) {
  const selectedYougovBrands: any = {};
  [mainBrand, ...competitors].forEach((brand) => {
    const slugifiedBrand = getSlugifiedString(brand);
    const yougovBrand = yougovBrands.find(
      (yougovBrand) => getSlugifiedString(yougovBrand.name) === slugifiedBrand
    );
    if (yougovBrand) {
      selectedYougovBrands[slugifiedBrand] = { ...yougovBrand, region: "se" };
    }
  });

  const selectedMeltwaterBrands: any = {};

  selectedMeltwaterBrands[getSlugifiedString(mainBrand)] = {
    base: mainBrand,
    brandNames: [{ name: mainBrand, tableName: getSlugifiedString(mainBrand) }],
    // ppl: industry?.meltwaterQueries.ppl,
    // prc: industry?.meltwaterQueries.prc,
    // prd: industry?.meltwaterQueries.prd,
    // prf: industry?.meltwaterQueries.prf,
    // prm: industry?.meltwaterQueries.prm,
    // prp: industry?.meltwaterQueries.prp,
    // plc: industry?.meltwaterQueries.plc,
    include: suggestions[mainBrand]?.included ?? [],
    exclude: suggestions[mainBrand]?.excluded ?? [],
    keywords: suggestions[mainBrand]?.selected ?? [],
  };

  competitors.forEach((competitor) => {
    selectedMeltwaterBrands[getSlugifiedString(competitor)] = {
      base: competitor,
      brandNames: [
        { name: competitor, tableName: getSlugifiedString(competitor) },
      ],
      include: suggestions[competitor]?.included ?? [],
      exclude: suggestions[competitor]?.excluded ?? [],
      keywords: suggestions[competitor]?.selected ?? [],
    };
  });

  return {
    uid: userId,
    brand: {
      name: getSlugifiedString(mainBrand),
      displayName: mainBrand,
      industry: industry?.label || "",
      industryId: industryId,
      searchName: name,
      marketId: marketCode,
    },
    competitors: competitors.map((competitor) =>
      getSlugifiedString(competitor)
    ),
    yougov: selectedYougovBrands,
    meltwater: selectedMeltwaterBrands,
  };
}
