import firebase from "firebase/app";
import { Campaign } from "./campaignsSlice";

export async function getCampaigns(searchId: string) {
  const collection = await firebase.firestore().collection("campaigns").where("searchId", "==", searchId).get();

  return collection.docs
    .reduce((acc, doc) => {
      const { name, startDate, endDate, createdAt } = doc.data();

      if (typeof name === "string" && name && !isNaN(Date.parse(startDate))) {
        acc.push({
          id: doc.id,
          name,
          startDate,
          endDate: !isNaN(Date.parse(endDate)) ? endDate : "",
          createdAt: !isNaN(Date.parse(createdAt)) ? createdAt : "",
        });
      }

      return acc;
    }, [] as Campaign[])
    .slice(0, 3);
}

export async function addCampaign({ searchId, name, startDate, endDate }: { searchId: string } & Omit<Campaign, "id" | "createdAt">) {
  const createdAt = new Date().toISOString();

  const doc = await firebase.firestore().collection("campaigns").add({
    searchId,
    name,
    startDate,
    endDate,
    createdAt,
  });

  return {
    id: doc.id,
    createdAt,
  };
}

export async function updateCampaign({ id, name, startDate, endDate }: Omit<Campaign, "createdAt">) {
  const campaign = {
    name,
    startDate,
    endDate,
  };

  await firebase.firestore().collection("campaigns").doc(id).update(campaign);
}

export async function deleteCampaign(id: Campaign["id"]) {
  await firebase.firestore().collection("campaigns").doc(id).delete();
}
