import { useContext, useState } from "react";
import styles from "./PasswordReset.module.scss";
import context from "../../../context";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { resetPassword } from "../../../store/actions";
import { Input } from "../../../components";
import { notify } from "react-notify-toast";
import { useTemporaryErrors } from "../../../hooks";
import { WebsiteLogoVertical } from "../../../assets/icons";
import { withError } from "../../../hocs";

const InputWithError = withError(Input);

interface Props {
  resetPassword: typeof resetPassword;
}

const PasswordReset = ({ resetPassword }: Props) => {
  const { setIsGlobalPreloaderShown } = useContext(context);

  const [isLinkSent, setIsLinkSent] = useState(false);
  const [email, setEmail] = useState("");

  const { errors, setErrors } = useTemporaryErrors(3000);

  function validate() {
    const validationErrors: typeof errors = {};
    if (!email.trim().length) validationErrors.email = "Required";
    return validationErrors;
  }

  async function handleSubmitForm() {
    const errors = validate();
    if (Object.keys(errors).length) {
      setErrors(errors);
      return;
    }

    setIsGlobalPreloaderShown(true);
    try {
      await resetPassword(email);
      setIsLinkSent(true);
    } catch (error) {
      notify.show(error || "Oops, something went wrong", "error", 5000);
    }
    setIsGlobalPreloaderShown(false);
  }

  return (
    <div className={styles.passwordReset}>
      <WebsiteLogoVertical className={styles.logo} />
      {!isLinkSent ? (
        <>
          <div className={styles.title}>Forgot password?</div>
          <form
            name="resetPassword"
            autoComplete="off"
            onSubmit={(e) => {
              e.preventDefault();
              handleSubmitForm();
            }}
          >
            <InputWithError
              className={styles.input}
              type="email"
              value={email}
              changeHandler={(value) => setEmail(value)}
              placeholder="Email"
              error={errors.email}
            />
            <button className={styles.button}>SEND RESET LINK</button>
          </form>
        </>
      ) : (
        <>
          <div className={styles.title}>Check your email!</div>
          <div className={styles.note}>
            Further instructions have been
            <br /> sent to your email address.
          </div>
        </>
      )}
      <Link className={styles.link} to="/login">
        Take me back to sign in
      </Link>
    </div>
  );
};

export default connect(null, { resetPassword })(PasswordReset);
