export function parseInteger(value: any): number | undefined {
  if (Number.isInteger(value)) return value;
}

export function parseDate(value: any): Date | undefined {
  if (!isNaN(Date.parse(value))) return new Date(value);
}

export function parseNonEmptyStringsArray(value: unknown): string[] | undefined {
  if (Array.isArray(value)) return value.filter(item => item && typeof item === "string");
}
