import { FC, SVGProps, memo, useMemo, useState, useEffect } from "react";
import styles from "./Sidebar.module.scss";
import { NavLink } from "react-router-dom";
import {
  Menu,
  DoubleArrowLeft,
  Home,
  Dashboard,
  Widgets,
  MagnifierPlus,
  AI,
  Company,
  Plans,
  // Education,
  BookDemo,
  Faq,
  Logout,
} from "../assets/icons";
import { useSelector, useDispatch } from "react-redux";
import { logOut } from "../store/actions";
import cx from "classnames";

type Link = {
  icon: FC<SVGProps<SVGSVGElement>>;
  title: string;
  url: string;
};

export const Sidebar: FC = memo(() => {
  const hasEnterpriseDashboards = useSelector(
    ({ company }: Store.State) => !!company.enterpriseDashboards.length
  );

  const hasProcessedSearches = useSelector(
    ({ searches }: Store.State) =>
      !!searches.some((search) => !search.isPending)
  );

  const isUserAdmin = useSelector(
    ({ company, user }: Store.State) =>
      !!company.members.find(({ id }) => id === user.id)?.isAdmin
  );

  const dispatch = useDispatch();

  const topLinks: Link[] = useMemo(
    () => [
      {
        icon: Home,
        title: "Overview",
        url: "/overview",
      },
      ...(hasEnterpriseDashboards
        ? [
            {
              icon: Dashboard,
              title: "Enterprise",
              url: "/enterprise-dashboards",
            },
          ]
        : []),
      {
        icon: Dashboard,
        title: "Dashboards",
        url: "/blocks",
      },
      {
        icon: Widgets,
        title: "Widget library",
        url: "/widget-library",
      },
      {
        icon: MagnifierPlus,
        title: "New search",
        url: "/searches",
      },
    ],
    [hasEnterpriseDashboards]
  );

  const bottomLinks: Link[] = useMemo(
    () => [
      ...(hasProcessedSearches
        ? [
            {
              icon: AI,
              title: "AI trainer",
              url: "/ai-trainer",
            },
          ]
        : []),

      ...(isUserAdmin
        ? [
            {
              icon: Company,
              title: "Company",
              url: "/company",
            },
          ]
        : []),
      {
        icon: Plans,
        title: "Pricing",
        url: "/pricing",
      },
      // {
      //   icon: Education,
      //   title: "Education",
      //   url: "/education",
      // },
      {
        icon: BookDemo,
        title: "Book a demo",
        url: "/demo-booking",
      },
      {
        icon: Faq,
        title: "FAQ",
        url: "/faq",
      },
    ],
    [hasProcessedSearches, isUserAdmin]
  );

  const [isNarrow, setIsNarrow] = useState(() => {
    let savedIsNarrow = localStorage.getItem("Telescope.isNarrow");
    if (savedIsNarrow) savedIsNarrow = JSON.parse(savedIsNarrow);
    if (typeof savedIsNarrow !== "boolean") savedIsNarrow = null;

    return savedIsNarrow ?? false;
  });

  useEffect(() => {
    localStorage.setItem("Telescope.isNarrow", JSON.stringify(isNarrow));
  }, [isNarrow]);

  const [isOpen, setIsOpen] = useState(false);

  const links = (links: Link[]) =>
    links.map(({ icon: Icon, title, url }) => (
      <NavLink
        className={styles.item}
        activeClassName={styles.activeItem}
        title={title}
        to={url}
        onClick={() => setIsOpen(false)}
        key={title}
      >
        <Icon />
        <div>{title}</div>
      </NavLink>
    ));

  return (
    <>
      <Menu
        className={cx(styles.menu, { [styles.menuOpen!]: isOpen })}
        size={32}
        onClick={() => setIsOpen((prev) => !prev)}
      />
      <div className={cx(styles.sidebar, { [styles.sidebarClosed!]: !isOpen })}>
        <div
          className={cx(styles.content, { [styles.contentNarrow!]: isNarrow })}
        >
          <DoubleArrowLeft
            className={styles.arrow}
            onClick={() => setIsNarrow((prev) => !prev)}
          />
          <div className={styles.top}>{links(topLinks)}</div>
          <div className={styles.bottom}>
            {links(bottomLinks)}
            <div
              className={styles.item}
              style={{ marginTop: 40 }}
              title="Log out"
              onClick={() => dispatch(logOut())}
            >
              <Logout />
              <div>Log out</div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
});
