import axios from "../../axios";

const urlInsertion = process.env.REACT_APP_ENV;

export const postUser = (payload: { firstName: string; lastName: string; password: string; email: string; company?: string }) =>
  axios.post(`users-${urlInsertion}-api`, payload);

export const postSurveyRequest = (userId: string, searchId: string) =>
  axios.post(`email-service-${urlInsertion}-api/survey-request-email`, { userId, brandId: searchId });
