import { FC, PropsWithChildren } from "react";
import styles from "./BasicModal.module.scss";

interface Props {
  redButton?: {
    text?: string;
    onClicked: () => void;
  };
  greenButton?: {
    text?: string;
    onClicked: () => void;
  };
}

export const BasicModal: FC<PropsWithChildren<Props>> = (props) => {
  return (
    <div className={styles.basicModal}>
      {props.children}
      <div className={styles.buttonsWrapper}>
        {props.redButton && (
          <button
            className={styles.button}
            style={{ color: "#ef336a", borderColor: "#ef336a" }}
            onClick={props.redButton.onClicked}
          >
            {props.redButton.text ?? "Cancel"}
          </button>
        )}
        {props.greenButton && (
          <button
            className={styles.button}
            style={{ color: "#41b167", borderColor: "#41b167" }}
            onClick={props.greenButton.onClicked}
          >
            {props.greenButton.text ?? "Yes"}
          </button>
        )}
      </div>
    </div>
  );
};
