import React, { useContext, useState, useEffect } from "react";
import styles from "./App.module.scss";
import { withRouter, Switch, Route, Redirect } from "react-router-dom";
import { connect } from "react-redux";
import * as actions from "../store/actions";
import context from "../context";
import { Header } from "./Header";
import Auth from "../containers/Auth/Auth";
import Home from "../containers/Home/Home";
import TermsAndConditionsOfUse from "../containers/TermsAndConditionsOfUse/TermsAndConditionsOfUse";
import { ModalWrapper } from "./ModalWrapper";
import { Preloader } from "../components";
import Notifications from "react-notify-toast";
import { getPreferredColorScheme } from "../utils";

interface Props {
  isAuthenticated: boolean;
  currentSearchId: Store.State["current"]["searchId"];
  setAuthObserver: () => void;
}

const App = ({ isAuthenticated, currentSearchId, setAuthObserver }: Props) => {
  const { modalElement, isGlobalPreloaderShown } = useContext(context);

  const [isDarkTheme, setIsDarkTheme] = useState(() => {
    let savedIsDarkTheme = localStorage.getItem("Telescope.isDarkTheme");
    if (savedIsDarkTheme) savedIsDarkTheme = JSON.parse(savedIsDarkTheme);
    if (typeof savedIsDarkTheme !== "boolean") savedIsDarkTheme = null;

    return savedIsDarkTheme ?? getPreferredColorScheme() ?? false;
  });

  useEffect(() => {
    localStorage.setItem("Telescope.isDarkTheme", JSON.stringify(isDarkTheme));
  }, [isDarkTheme]);

  const [isShown, setIsShown] = useState(false);

  useEffect(() => {
    (async () => {
      await setAuthObserver();
      setIsShown(true);
    })();
  }, [setAuthObserver]);

  useEffect(() => {
    if (currentSearchId)
      sessionStorage["Telescope.lastSearchId"] = currentSearchId;
  }, [currentSearchId]);

  return isShown ? (
    <div
      className={styles.app}
      {...{ "dark-theme": isDarkTheme ? "" : undefined }}
    >
      <div
        className={styles.content}
        style={{ ...(modalElement && { filter: "blur(4px)" }) }}
      >
        <Header
          isDarkTheme={isDarkTheme}
          onThemeIconClicked={(isDarkTheme) => setIsDarkTheme(isDarkTheme)}
        />
        <div className={styles.section}>
          <Switch>
            <Route exact path="/terms-and-conditions-of-use">
              <TermsAndConditionsOfUse />
            </Route>
            <Route path="/">{isAuthenticated ? <Home /> : <Auth />}</Route>
            <Redirect to="/" />
          </Switch>
        </div>
      </div>
      {modalElement && <ModalWrapper>{modalElement}</ModalWrapper>}
      {isGlobalPreloaderShown && <Preloader modifier="global" />}
      <Notifications options={{ zIndex: 16, timeout: 3000 }} />
    </div>
  ) : null;
};

const mapStateToProps = (state: Store.State) => ({
  isAuthenticated: Boolean(state.user.id),
  currentSearchId: state.current.searchId,
});

export default withRouter(
  connect(mapStateToProps, {
    setAuthObserver: actions.setAuthObserver,
  })(App)
);
