import { forwardRef, CSSProperties, ForwardedRef } from "react";
import classes from "./WordTile.module.scss";

interface Props {
  className?: string;
  style?: CSSProperties;
  word: string;
  clickHandler?: () => void;
  sign?: "plus" | "minus";
}

export const WordTile = forwardRef(({ className = "", style, word, clickHandler, sign }: Props, ref: ForwardedRef<HTMLDivElement>) => {
  return (
    <div
      ref={ref}
      className={`${classes.wordTile} ${clickHandler ? classes.wordTileClickable : ""} ${className}`}
      style={style}
      title={word}
      onClick={clickHandler}
    >
      <div className={classes.text} style={{ width: `calc(100% - ${sign ? "23" : "0"}px)` }}>
        {word}
      </div>
      {sign && (
        <div className={classes.circle}>
          <div />
          {sign === "plus" && <div />}
        </div>
      )}
    </div>
  );
});
