import React from "react";
import classes from "./WidgetOptions.module.scss";
import { SVG } from "../../../index";

interface IProps {
  className?: string;
  style?: React.CSSProperties;
  options?: readonly {
    value: string;
    svgName?: SvgName;
  }[];
  value?: string;
  valueIndex?: number;
  clickHandler?: (value: any, index: number) => void;
}

export const WidgetOptions = ({ className = "", style, options = [], value, valueIndex, clickHandler }: IProps) => {
  return (
    <div className={`${classes.widgetOptions} ${className}`} style={style}>
      {options.map((option, i) => (
        <div
          className={`${classes.option} ${option.value === value || i === valueIndex ? classes.optionActive : ""}`}
          onClick={() => clickHandler?.call(null, option.value, i)}
          title={option.value}
          key={i}
        >
          {option.svgName && (
            <div className={classes.optionSvgWrapper}>
              <SVG name={option.svgName} />
            </div>
          )}
          <div>{option.value}</div>
        </div>
      ))}
    </div>
  );
};
