import React, { useState, useRef, useEffect } from "react";
import classes from "./CreateSearch.module.scss";
import { CloseIcon, Input } from "../../index";
import { useTemporaryErrors } from "../../../hooks";
import { withError } from "../../../hocs";

const InputWithError = withError(Input);

interface IProps {
  previousName?: string;
  brands?: string[];
  market?: string;
  industry?: string;
  clickRedButtonHandler?: () => void;
  clickGreenButtonHandler?: (name: string) => void;
}

export const CreateSearch = ({
  previousName,
  brands,
  market,
  industry,
  clickRedButtonHandler,
  clickGreenButtonHandler,
}: IProps) => {
  const [name, setName] = useState(previousName ?? "");

  const { errors, setErrors } = useTemporaryErrors(3000);

  const inputRef = useRef<HTMLInputElement>(null);

  useEffect(() => {
    inputRef.current?.focus();
  }, []);

  function handleClickGreenButton() {
    const _name = name.trim();
    if (!_name) {
      setErrors({ name: "Required" });
      return;
    }
    clickGreenButtonHandler?.call(null, _name);
  }

  return (
    <div className={classes.createSearch}>
      <CloseIcon className={classes.closeIcon} />
      <div className={classes.inputWrapper}>
        <div className={classes.label}>SAVE SEARCH AS:</div>
        <InputWithError
          ref={inputRef}
          className={classes.input}
          value={name}
          changeHandler={(value) => setName(value)}
          error={errors.name}
        />
      </div>
      <div className={classes.text}>
        Main company to track: {brands?.[0] || "..."}
        <br />
        <br />
        Company to compare to: {brands?.slice(1).join(", ") || "..."}
        <br />
        <br />
        Market: {market || "..."}
        <br />
        <br />
        Industry: {industry || "..."}
        <br />
        <br />
        Stay put!
        <br />
        We will send you a notification when were done collection all this
        data. Estimated time is 48 to 72 hours.
      </div>
      <div className={classes.buttonsWrapper}>
        <button className={classes.button} onClick={clickRedButtonHandler}>
          CANCEL
        </button>
        <button className={classes.button} onClick={handleClickGreenButton}>
          OK! LET'S GO
        </button>
      </div>
    </div>
  );
};
