import firebase from "firebase/app";
import * as yup from "yup";
import { Dashboard } from "./dashboardsSlice";

const dashboardSchema = yup
  .object({
    name: yup.string().required(),
    isPrivate: yup.boolean().required(),
    folderId: yup.string().nullable(true).defined(),
    widgetIds: yup.array(yup.string().required()).required(),
    configs: yup.object().required(),
    userId: yup.string().required(),
    searchId: yup.string().required(),
    createdAt: yup.string().required(),
    updatedAt: yup.string().required(),
  })
  .noUnknown();

export async function createDashboard(
  payload: Omit<Dashboard, "id" | "createdAt" | "updatedAt"> & {
    companyId: string;
  }
) {
  let createdAt, updatedAt;

  createdAt = updatedAt = new Date().toISOString();

  const doc = await firebase.firestore().collection("dashboards").add({
    name: payload.name,
    isPrivate: payload.isPrivate,
    folderId: payload.folderId,
    widgetIds: payload.widgetIds,
    configs: payload.configs,
    companyId: payload.companyId,
    userId: payload.userId,
    searchId: payload.searchId,
    createdAt,
    updatedAt,
  });

  return {
    id: doc.id,
    createdAt,
    updatedAt,
  };
}

export async function readDashboards(companyId: string) {
  const collection = await firebase
    .firestore()
    .collection("dashboards")
    .where("companyId", "==", companyId)
    .get();

  return collection.docs.reduce<Dashboard[]>((acc, doc) => {
    try {
      const validatedData = dashboardSchema.validateSync(doc.data());
      acc.push({ id: doc.id, ...validatedData });
    } catch (error) {
      console.dir(error);
    }

    return acc;
  }, []);
}

export async function updateDashboard({
  id,
  changes,
}: UpdateEntity<Dashboard>) {
  changes = {
    updatedAt: new Date().toISOString(),
    ...changes,
  };

  await firebase.firestore().collection("campaigns").doc(id).update(changes);
}

export async function deleteDashboard(id: Dashboard["id"]) {
  await firebase.firestore().collection("dashboards").doc(id).delete();
}
