import actionTypes from "../actionTypes";

const initialState: Store.Company = {
  id: "",
  name: "",
  subscriptionPlanIds: {
    current: ""
  },
  enterpriseDashboards: [],
  members: []
};

const setCompanyData = (state: Store.Company, action: any) => {
  return { ...state, ...action.companyData };
};

const removeCompanyData = () => initialState;

const reducer = (state = initialState, action: any) => {
  switch (action.type) {
    case actionTypes.SET_COMPANY_DATA:
      return setCompanyData(state, action);
    case actionTypes.REMOVE_COMPANY_DATA:
      return removeCompanyData();
    default:
      return state;
  }
};

export default reducer;
