import domToImage from "dom-to-image";

export async function downloadDomAsImage(element: HTMLElement, imageName = "Screenshot", backgroundColor = "#20263b") {
  element.classList.add("safeFont");
  const dataUrl = await domToImage.toJpeg(element, { bgcolor: backgroundColor });
  element.classList.remove("safeFont");
  const link = document.createElement("a");
  link.download = imageName + ".jpeg";
  link.href = dataUrl;
  link.click();
}
