import React from "react";
import classes from "./RequestServeyModal.module.scss";
import { CloseIcon, Image } from "../../index";

interface Props {
  text?: {
    main?: string;
    secondary?: string;
  };
  clickRedButtonHandler?: () => void;
  clickGreenButtonHandler?: () => void;
}

export const RequestServeyModal = ({ text, clickRedButtonHandler, clickGreenButtonHandler }: Props) => {
  return (
    <div className={classes.requestServeyModal}>
      <CloseIcon className={classes.closeIcon} color="#efe9ea" />
      <div className={classes.content}>
        <div className={classes.textWrapper}>
          <div className={classes.text}>
            {text?.main && <p className={classes.textMain} dangerouslySetInnerHTML={{ __html: text.main }} />}
            {text?.secondary && <p className={classes.textSecondary} dangerouslySetInnerHTML={{ __html: text.secondary }} />}
          </div>
        </div>
        <div className={classes.imageWrapper}>
          <Image className={classes.image} name="birdsScientists" />
        </div>
        <div className={classes.redButtonWrapper}>
          {clickRedButtonHandler && (
            <button className={`${classes.button} ${classes.buttonRed}`} onClick={clickRedButtonHandler}>
              No, not right now, thank you
            </button>
          )}
        </div>
        <div className={classes.greenButtonWrapper}>
          {clickGreenButtonHandler && (
            <button className={`${classes.button} ${classes.buttonGreen}`} onClick={clickGreenButtonHandler}>
              Yes! Let's do this
            </button>
          )}
        </div>
      </div>
    </div>
  );
};
