import { useContext, useState, useEffect, useMemo } from "react";
import styles from "./Signup.module.scss";
import { connect } from "react-redux";
import * as actions from "../../../store/actions";
import context from "../../../context";
import { Input } from "../../../components";
import { Link, useHistory, useLocation } from "react-router-dom";
import { notify } from "react-notify-toast";
import { useTemporaryErrors } from "../../../hooks";
import { WebsiteLogoVertical } from "../../../assets/icons";
import * as yup from "yup";
import { withError } from "../../../hocs";

const InputWithError = withError(Input);

const emailSchema = yup.string().email();

interface Props {
  companyName: Store.Company["name"];
  readCompany: typeof actions.readCompany;
  removeCompanyData: typeof actions.removeCompanyData;
  signUp: typeof actions.signUp;
  logIn: typeof actions.logIn;
}

const Signup = ({
  companyName,
  readCompany,
  removeCompanyData,
  signUp,
  logIn,
}: Props) => {
  const history = useHistory();
  const location = useLocation();

  const companyId = useMemo(() => {
    const search = location.search;
    const params = new URLSearchParams(search);
    const companyId = params.get("company");
    if (companyId) return companyId;
  }, [location.search]);

  const { setIsGlobalPreloaderShown } = useContext(context);

  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");
  const [email, setEmail] = useState("");
  const [password1, setPassword1] = useState("");
  const [password2, setPassword2] = useState("");

  const { errors, setErrors } = useTemporaryErrors(3000);

  useEffect(() => {
    if (!companyId) return;
    (async () => {
      setIsGlobalPreloaderShown(true);
      try {
        await readCompany(companyId);
      } catch (error) {
        console.dir(error);
        notify.show("Oops, something went wrong", "error");
      }
      setIsGlobalPreloaderShown(false);
    })();

    return () => {
      removeCompanyData();
    };
  }, [companyId, setIsGlobalPreloaderShown, readCompany, removeCompanyData]);

  function validate() {
    const validationErrors: typeof errors = {};
    if (!firstName.trim().length) validationErrors.firstName = "Required";
    if (!lastName.trim().length) validationErrors.lastName = "Required";
    if (!emailSchema.isValidSync(email))
      validationErrors.email = "Should be a valid email address";
    if (!email.trim().length) validationErrors.email = "Required";
    const minPasswordLength = 8;
    if (password1.trim().length < minPasswordLength)
      validationErrors.password1 = `Password must be at least ${minPasswordLength} characters`;
    if (!password1.trim().length) validationErrors.password1 = "Required";
    if (password1 !== password2)
      validationErrors.password2 = "Passwords don't match";
    if (!password2.trim().length) validationErrors.password2 = "Required";
    return validationErrors;
  }

  async function handleSubmitForm() {
    const errors = validate();
    if (Object.keys(errors).length) {
      setErrors(errors);
      return;
    }
    setIsGlobalPreloaderShown(true);
    try {
      await signUp(
        firstName.trim(),
        lastName.trim(),
        email.trim(),
        password1.trim(),
        companyId
      );
      await logIn(email.trim(), password1.trim());
    } catch (error) {
      console.dir(error);
      notify.show(
        error.response?.data?.error ||
          error.response?.data ||
          "Oops, something went wrong",
        "error",
        5000
      );
    }
    setIsGlobalPreloaderShown(false);
  }

  return (
    <div className={styles.signup}>
      <div className={styles.logoWrapper}>
        <WebsiteLogoVertical className={styles.logo} />
      </div>
      <div className={styles.title}>Activate your Competitive Compass</div>
      <div className={styles.note1}>
        {companyId
          ? `After registration, your account will be the part of ${
              companyName ? companyName + " " : ""
            }company.`
          : "30 day free trial. No credit card needed."}
      </div>
      <form
        name="signUp"
        autoComplete="off"
        onSubmit={(e) => {
          e.preventDefault();
          handleSubmitForm();
        }}
      >
        <div className={styles.inputsWrapper}>
          <InputWithError
            className={styles.input}
            value={firstName}
            changeHandler={(x) => setFirstName(x)}
            placeholder="First name"
            error={errors.firstName}
          />
          <InputWithError
            className={styles.input}
            value={lastName}
            changeHandler={(x) => setLastName(x)}
            placeholder="Last name"
            error={errors.lastName}
          />
          <InputWithError
            className={styles.input}
            value={email}
            changeHandler={(x) => setEmail(x)}
            placeholder="Email"
            error={errors.email}
          />
          <InputWithError
            className={styles.input}
            type="password"
            value={password1}
            changeHandler={(value) => setPassword1(value)}
            placeholder="Password"
            error={errors.password1}
          />
          <InputWithError
            className={styles.input}
            type="password"
            value={password2}
            changeHandler={(value) => setPassword2(value)}
            placeholder="Rewrite password"
            error={errors.password2}
          />
        </div>
        <button className={`${styles.button} ${styles.buttonGreen}`}>
          SIGN UP
        </button>
      </form>
      <div className={styles.note2}>
        By signing up, you agree to My Telescopes{" "}
        <Link to="/terms-and-conditions-of-use">
          <b>Terms and conditions of use</b>
        </Link>{" "}
        and{" "}
        <a
          href="https://mytelescope.io/8privacypolicy"
          rel="noopener noreferrer"
        >
          <b>Privacy policy</b>
        </a>
        .
      </div>
      <button
        className={`${styles.button} ${styles.buttonBlue}`}
        onClick={() => history.push("/login")}
      >
        ALREADY HAVE AN ACCOUNT? - LOG IN
      </button>
    </div>
  );
};

const mapStateToProps = ({ company }: Store.State) => ({
  companyName: company.name,
});

export default connect(mapStateToProps, {
  readCompany: actions.readCompany,
  removeCompanyData: actions.removeCompanyData,
  signUp: actions.signUp,
  logIn: actions.logIn,
})(Signup);
