import styles from "./withDrop.module.scss";
import { useDrop } from "react-dnd";
import cx from "classnames";

interface DropProps {
  accept: string[];
  onDropped?: (item: any) => void;
}

export const withDrop = <T extends object & { className?: string }>(
  Component: React.ComponentType<T>
) => ({ accept, onDropped, className, ...props }: T & DropProps) => {
  const [{ isOver, canDrop }, drop] = useDrop(
    () => ({
      accept,
      drop: onDropped,
      collect: (monitor) => ({
        isOver: monitor.isOver(),
        canDrop: monitor.canDrop(),
      }),
    }),
    [accept, onDropped]
  );

  return (
    <Component
      ref={drop}
      {...(props as T)}
      className={cx(className, {
        [styles.withDrop!]: canDrop,
        [styles.withDropOver!]: isOver,
      })}
    />
  );
};
