import React, { useState, useMemo, useEffect, useRef } from "react";
import classes from "./CompetitivenessSpeedometer.module.scss";
import { normalizeNumber } from "../../../utils";

const TICK_COLORS = [
  "#ef336a",
  "#dc3979",
  "#c93f87",
  "#b54496",
  "#a24aa4",
  "#8f50b3",
  "#7c56c1",
  "#695ccf",
  "#5561de",
  "#4267ec",
  "#2f6dfb",
  "#3f77ef",
  "#4f80e3",
  "#5f8ad7",
  "#6f94cb",
  "#7f9ec0",
  "#8ea7b4",
  "#9eb1a8",
  "#aebb9c",
  "#bec490",
  "#cece84",
] as const;

const mainTicks = [...Array(5).keys()].map((i) => {
  const number = -100 + i * 50;
  return {
    deg: -30 + i * 60,
    number: (number > 0 ? "+" : "") + number,
  };
});

const ticks = [...Array(21).keys()].map((i) => {
  const number = -100 + i * 10;
  return {
    deg: -30 + i * 12,
    number: (number > 0 ? "+" : "") + number,
    color: TICK_COLORS[i],
  };
});

interface IProps {
  className?: string;
  style?: React.CSSProperties;
  data: readonly {
    brandName: string;
    values: {
      prev: number;
      current: number;
      delta: number;
    };
    color: string;
  }[];
}

export const CompetitivenessSpeedometer = ({ className = "", style, data }: IProps) => {
  const [inactiveBrandNames, setInactiveBrandNames] = useState<string[]>([]);
  const [mainBrandDegree, setMainBrandDegree] = useState(-30);
  const [isRoundPointerHovered, setIsRoundPointerHovered] = useState(false);
  const [scale, setScale] = useState<number>();

  const brands = useMemo(
    () =>
      data
        .filter(({ brandName }) => !inactiveBrandNames.includes(brandName))
        .map(({ values, color }, i, arr) => {
          const SIZE_INCREMENT = 64;
          const size = 64 + SIZE_INCREMENT * i;
          const number = normalizeNumber(values.current, 100);
          return {
            degree: convertNumberToDegree(number),
            number: (number > 0 ? "+" : "") + number,
            offset: -size / 2,
            size,
            borderWidth: SIZE_INCREMENT / 2 - 2,
            zIndex: arr.length - i,
            color,
            lineLength: 232 - size / 2,
          };
        }),
    [data, inactiveBrandNames]
  );

  useEffect(() => {
    (async () => {
      setMainBrandDegree(-30);
      setTimeout(() => {
        const number = normalizeNumber(data[0]!.values.current, 100);
        const degree = convertNumberToDegree(number);
        setMainBrandDegree(degree);
      }, 10);
    })();
  }, [data]);

  const wrapperRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const _setScale = () => {
      const wrapperWidth = wrapperRef.current?.clientWidth!;
      setScale(wrapperWidth / 680);
    };
    _setScale();
    window.addEventListener("resize", _setScale);
    return () => {
      window.removeEventListener("resize", _setScale);
    };
  }, []);

  function convertNumberToDegree(number: number) {
    return (number * 120) / 100 + 90;
  }

  function handleClickLabel(brandName: string) {
    setInactiveBrandNames(
      inactiveBrandNames.includes(brandName) ? inactiveBrandNames.filter((brand) => brand !== brandName) : [...inactiveBrandNames, brandName]
    );
  }

  const CurvedText = (text: string, angle = 0, color?: string) => {
    const LETTER_OFFSET_ANGLE = 1.8;
    const letters = text.split("").map((letter, i) => ({
      letter,
      deg: angle - (text.length / 2) * LETTER_OFFSET_ANGLE + i * LETTER_OFFSET_ANGLE,
    }));
    return letters.map(({ letter, deg }, i) => (
      <div className={classes.curvedLetter} style={{ color, transform: `rotate(${deg}deg) translate(-320px)` }} key={i}>
        <div>{letter}</div>
      </div>
    ));
  };

  return data.length ? (
    <div className={`${classes.competitivenessSpeedometer} ${className}`} style={style}>
      <div className={classes.labelsWrapper}>
        <div className={classes.labels}>
          {data.map(({ brandName, color }, i) => (
            <div
              className={`${classes.label} ${inactiveBrandNames.includes(brandName) ? classes.labelInactive : ""}`}
              style={{ maxWidth: 100 / data.length + "%" }}
              onClick={i !== 0 ? () => handleClickLabel(brandName) : undefined}
              title={brandName}
              key={i}
            >
              <div style={{ backgroundColor: color }} />
              <div>{brandName}</div>
            </div>
          ))}
        </div>
      </div>
      <div ref={wrapperRef} className={classes.widgetWrapperOuter}>
        {!!scale && (
          <div className={classes.widgetWrapperInner}>
            <div className={classes.widget} style={{ transform: `scale(${scale})` }}>
              <div className={classes.center}>
                {CurvedText("Market is slow", 0, TICK_COLORS[2])}
                {CurvedText("Slow to neutral", 60, TICK_COLORS[7])}
                {CurvedText("Neutral to quite good", 120, TICK_COLORS[12])}
                {CurvedText("Good to fantastic", 180, TICK_COLORS[17])}
                {mainTicks.map(({ deg, number }, i) => (
                  <div className={classes.mainTick} style={{ transform: `rotate(${deg}deg) translate(-320px)` }} key={i}>
                    <div style={{ transform: `rotate(${-deg}deg)` }}>{number}</div>
                  </div>
                ))}
                {ticks.map(({ deg, number, color }, i) => (
                  <div className={classes.tick} style={{ backgroundColor: color, transform: `rotate(${deg}deg) translate(-280px)` }} key={i}>
                    <div style={{ transform: `rotate(${-deg}deg)` }}>{number}</div>
                  </div>
                ))}
                <div onMouseEnter={() => setIsRoundPointerHovered(true)} onMouseLeave={() => setIsRoundPointerHovered(false)}>
                  {brands.slice(1).map(({ degree, number, offset, size, borderWidth, zIndex, color, lineLength }, i) => (
                    <div
                      className={classes.roundPointer}
                      style={{
                        left: offset,
                        top: offset,
                        width: size,
                        height: size,
                        borderColor: color,
                        zIndex,
                        transform: `rotate(${degree}deg)`,
                      }}
                      key={i}
                    >
                      <div style={{ borderWidth, borderColor: color }} />
                      <div style={{ transform: `rotate(${-degree}deg)` }}>{number}</div>
                      <div style={{ left: -lineLength, width: lineLength, backgroundColor: color }} />
                      <div style={{ left: -lineLength - 16, backgroundColor: color }} />
                    </div>
                  ))}
                </div>
                <div
                  className={classes.arrowPointer}
                  style={{
                    left: brands[0]!.offset,
                    top: brands[0]!.offset,
                    width: brands[0]!.size,
                    height: brands[0]!.size,
                    backgroundColor: brands[0]!.color,
                    zIndex: brands[0]!.zIndex,
                    transform: `rotate(${mainBrandDegree}deg)`,
                  }}
                >
                  <div style={{ color: "#20263b", transform: `rotate(${-mainBrandDegree}deg)` }}>{brands[0]!.number}</div>
                  <div
                    style={{
                      left: -brands[0]!.lineLength - 16,
                      borderRightWidth: brands[0]!.lineLength + 20,
                      borderRightColor: brands[0]!.color,
                      opacity: isRoundPointerHovered ? 0.5 : 1,
                    }}
                  />
                </div>
              </div>
            </div>
          </div>
        )}
      </div>
      <div className={classes.message}>
        YOUR MONTHLY CHANGE:{" "}
        <b>
          {data[0]!.values.delta > 0 ? "+" : ""}
          {data[0]!.values.delta}
        </b>
      </div>
      <div className={classes.explanation}>
        <div>NO</div>
        <div />
        <div>Willingness to buy</div>
        <div />
        <div>YES</div>
      </div>
    </div>
  ) : null;
};
