export async function getCompetitivenessWidget(
  searchId: string,
  brands: Store.Brands
): Promise<Store.Widgets["competitiveness"]> {
  const parsedData: Store.Widgets["competitiveness"] = {};

  const doc = await firestore()
    .collection("result-competitiveness")
    .doc(searchId)
    .get();

  const data = doc.data();

  // competitiveness linechart
  const competitivenessLineChartData = data?.lineChartCompetitiveness;

  if (Array.isArray(competitivenessLineChartData)) {
    const parsedCompetitivenessLineChartData: Store.Widgets["competitiveness"]["competitivenessLineChart"] = [];

    competitivenessLineChartData.forEach((item) => {
      const parsedItem = parseDataItem(item);

      if (parsedItem) {
        parsedCompetitivenessLineChartData.push(parsedItem);
      }
    });

    const oneYearData = getLineChartDataForOneYear(
      parsedCompetitivenessLineChartData
    );

    if (oneYearData.length) {
      parsedData.competitivenessLineChart = oneYearData;
    }
  }

  //  linechart
  const interestLineChartData = data?.lineChartInterest;

  if (Array.isArray(interestLineChartData)) {
    const parsedInterestLineChartData: Store.Widgets["competitiveness"]["interestLineChart"] = [];

    interestLineChartData.forEach((item) => {
      const parsedItem = parseDataItem(item);

      if (parsedItem) {
        parsedInterestLineChartData.push(parsedItem);
      }
    });

    const oneYearData = getLineChartDataForOneYear(parsedInterestLineChartData);

    if (oneYearData.length) {
      parsedData.interestLineChart = oneYearData;
    }
  }

  // marketometer
  const marketometerChartData = data?.marketometerSet;

  // if (marketometerChartData) {
  //   const thisMonthItem = parseDataItem(marketometerChartData.thisMonth);
  //   const oneMonthAgoItem = parseDataItem(marketometerChartData.oneMonthAgo);

  //   if (thisMonthItem && oneMonthAgoItem) {
  //     parsedData.marketometerChart = {
  //       thisMonth: thisMonthItem,
  //       oneMonthAgo: oneMonthAgoItem,
  //       twoMonthsAgo: parseDataItem(marketometerChartData.twoMonthsAgo),

  //       sixMonthsAgo: parseDataItem(marketometerChartData.sixMonthsAgo),

  //       oneYearAgo: parseDataItem(marketometerChartData.oneYearAgo),
  //     };
  //   }
  // }

  // --------replace later with the code above
  const thisMonthItem =
    parseDataItem(marketometerChartData?.thisMonth) ||
    parsedData.competitivenessLineChart?.[
      parsedData.competitivenessLineChart.length - 1
    ];

  const oneMonthAgoItem =
    parseDataItem(marketometerChartData?.oneMonthAgo) ||
    parsedData.competitivenessLineChart?.[
      parsedData.competitivenessLineChart.length - 5
    ];

  if (thisMonthItem && oneMonthAgoItem) {
    parsedData.marketometerChart = {
      thisMonth: thisMonthItem,
      oneMonthAgo: oneMonthAgoItem,
      twoMonthsAgo:
        parseDataItem(marketometerChartData?.twoMonthsAgo) ||
        parsedData.competitivenessLineChart?.[
          parsedData.competitivenessLineChart.length - 10
        ],
      sixMonthsAgo:
        parseDataItem(marketometerChartData?.sixMonthsAgo) ||
        parsedData.competitivenessLineChart?.[
          parsedData.competitivenessLineChart.length - 27
        ],
      oneYearAgo:
        parseDataItem(marketometerChartData?.oneYearAgo) ||
        parsedData.competitivenessLineChart?.[
          parsedData.competitivenessLineChart.length - 53
        ],
    };
  }
  // ---------------------------------------------

  return parsedData;

  // utils
  function parseDataItem(item: any) {
    const date = item?.date;
    if (!Date.parse(date)) return;

    const values: { [brandName: string]: number } = {};

    brands.forEach(({ id, name }) => {
      const brandValue = item?.values?.[id];
      if (!Number.isFinite(brandValue)) return;

      values[name] = brandValue as number;
    });

    if (!Object.keys(values).length) return;

    return {
      date,
      values,
    };
  }
}

export async function getShareOfSearchWidget(
  searchId: string,
  brands: Store.Brands
): Promise<Store.Widgets["shareOfSearch"]> {
  const doc = await firestore()
    .collection("result-search-share")
    .doc(searchId)
    .get();

  const data = doc.data();

  return parseShareOfSearchWidget(data, brands);
}

export async function getShareOfSearchWidgetInstantly({
  marketCode,
  brands,
}: {
  marketCode: string;
  brands: {
    names: string[];
    suggestions: string[];
  }[];
}): Promise<Store.Widgets["shareOfSearch"]> {
  const payload = {
    market: marketCode,
    companies: brands.map(({ names, suggestions }) => {
      const slugifiedBrandName = getSlugifiedString(names[0]!);
      return {
        table: slugifiedBrandName,
        sourceTables: [slugifiedBrandName],
        keywords: suggestions.length ? suggestions : [names[0]!],
      };
    }),
  };

  const { data } = await axios.post(
    `search-share-widget-${urlInsertion}-api/get-data`,
    payload
  );

  return parseShareOfSearchWidget(
    data,
    brands.map(({ names }) => ({
      id: getSlugifiedString(names[0]!) + "_" + marketCode,
      name: names[0]!,
    }))
  );
}

export async function calculateShareOfSearchWidget(searchId: string) {
  return axios.post(`search-share-widget-${urlInsertion}-api/update-one`, {
    brandId: searchId,
  });
}

function parseShareOfSearchWidget(
  data: firestore.DocumentData | undefined,
  brands: Store.Brands
) {
  const parsedData: ShareOfSearch.Data = {
    pieChart: {},
    lineChart: [],
  };

  DATE_RANGES.forEach(({ value: range }) => {
    const rangeData = data?.pieChart?.[range];

    const values: {
      brandName: string;
      value: number;
    }[] = [];

    brands.forEach((brand) => {
      const value =
        rangeData?.values[brand.id]?.percentage || rangeData?.values[brand.id];
      if (typeof value !== "number") return;

      const additionalValue = rangeData?.values[brand.id]?.volume;

      values.push({
        brandName: brand.name,
        value: +value.toFixed(2),
        ...(typeof additionalValue === "number" && { additionalValue }),
      });
    });

    if (!values.length) return;

    parsedData.pieChart[range] = {
      startDate: rangeData?.startDate || "",
      endDate: rangeData?.endDate || "",
      values,
    };
  });

  const lineChartData = data?.lineChart;

  if (Array.isArray(lineChartData)) {
    lineChartData.forEach((item) => {
      const date = item?.date;
      if (!Date.parse(date)) return;

      const values: { [brandName: string]: number } = {};
      const additionalValues: { [brandName: string]: number } = {};

      brands.forEach(({ id, name }) => {
        const value = item?.values?.[id]?.percentage || item?.values?.[id];
        if (typeof value !== "number") return;

        values[name] = value;

        const additionalValue = item?.values[id]?.volume;

        additionalValues[name] = additionalValue;
      });

      if (!Object.keys(values).length) return;

      parsedData.lineChart.push({
        date,
        values,
        ...(Object.keys(additionalValues).length && { additionalValues }),
      });
    });
  }

  return {
    ...parsedData,
    lineChart: getLineChartDataForOneYear(parsedData.lineChart),
  };
}

