export async function getBusinessDriversWidget(
  searchId: string,
  brands: Store.Brands
): Promise<Store.Widgets["businessDrivers"]> {
  const parsedData: Store.Widgets["businessDrivers"] = [];

  const doc = await firestore()
    .collection("result-business-drivers")
    .doc(searchId)
    .get();

  const data = doc.data();

  if (data) {
    for (let i = 0; i < brands.length; i++) {
      const parsedBrandData: Store.Widgets["businessDrivers"][number]["brandData"] = {};

      // LINE CHART
      const parsedLineChartData: Store.Widgets["businessDrivers"][number]["brandData"]["lineChart"] = [];

      const lineChartData = data[brands[i]!.id]?.lineChart;

      if (Array.isArray(lineChartData)) {
        lineChartData.forEach((item) => {
          if (isNaN(Date.parse(item?.date))) return;

          const values: { [label: string]: number } = {};

          businessDriversParameters.forEach(({ name, key }) => {
            const value = item?.values[key]?.impact;

            if (!Number.isFinite(value)) return;

            values[name] = value;
          });

          // if (!Object.keys(values).length) return;

          parsedLineChartData.push({
            date: item.date,
            values,
          });
        });

        const oneYearData = getLineChartDataForOneYear(parsedLineChartData);

        if (oneYearData.length) {
          parsedBrandData.lineChart = oneYearData;
        }
      }

      //BAR CHART && QUADRANT CHART
      const brandData = data[brands[i]!.id]?.barChart;

      if (brandData) {
        const parsedBarChartData: Store.Widgets["businessDrivers"][number]["brandData"]["barChart"] = {};
        const parsedQuadrantChartData: Store.Widgets["businessDrivers"][number]["brandData"]["quadrantChart"] = {};

        outer: for (const { value: range } of DATE_RANGES) {
          const rangeData = brandData[range];

          if (!rangeData) continue;
          if (isNaN(Date.parse(rangeData.startDate))) continue;
          if (isNaN(Date.parse(rangeData.endDate))) continue;

          const values: {
            label: string;
            impact: number;
            potential: number;
          }[] = [];

          for (const { name, key } of businessDriversParameters) {
            const impact = rangeData?.values[key]?.impact;
            if (!Number.isFinite(impact)) continue outer;
            const potential = rangeData?.values[key]?.impact;
            if (!Number.isFinite(potential)) continue outer;

            values.push({
              label: name,
              impact,
              potential,
            });
          }

          //bar chart
          const barChartValues: { [label: string]: number } = {};

          values.forEach(
            ({ label, impact }) => (barChartValues[label] = impact)
          );

          parsedBarChartData[range] = {
            startDate: rangeData.startDate,
            endDate: rangeData.endDate,
            values: barChartValues,
          };

          // quadrant chart
          const impactValues = values.map(({ impact }) => impact);
          const potentialValues = values.map(({ potential }) => potential);

          const quadrantChartData = values
            .sort((a, b) => b.impact + b.potential - (a.impact + a.potential))
            .slice(0, 3)
            .map(({ label, impact, potential }) => ({
              label,
              values: {
                impact: getValueRelativePercentage(impact, impactValues),
                potential: getValueRelativePercentage(
                  potential,
                  potentialValues
                ),
              },
            }));

          parsedQuadrantChartData[range] = {
            startDate: rangeData.startDate,
            endDate: rangeData.endDate,
            data: quadrantChartData,
          };
        }

        if (Object.keys(parsedBarChartData).length) {
          parsedBrandData.barChart = parsedBarChartData;
        }

        if (Object.keys(parsedQuadrantChartData).length) {
          parsedBrandData.quadrantChart = parsedQuadrantChartData;
        }
      }

      if (Object.keys(parsedBrandData).length) {
        parsedData.push({
          brandName: brands[i]!.name,
          brandData: parsedBrandData,
        });
      } else {
        if (i === 0) break;
      }
    }
  }

  return parsedData;

  // utils
  function getValueRelativePercentage(value: number, values: number[]) {
    const PERCENTAGE_BORDER = 15;
    let minValue = Math.min(...values);
    let maxValue = Math.max(...values);
    if (minValue === maxValue) {
      minValue--;
      maxValue++;
    }
    const result =
      PERCENTAGE_BORDER +
      ((value - minValue) * (100 - PERCENTAGE_BORDER * 2)) /
        (maxValue - minValue);
    return +result.toFixed();
  }
}

export async function getAwarenessWidget(
  searchId: string,
  brands: Store.Brands
): Promise<Store.Widgets["awareness"]> {
  const doc = await firestore()
    .collection("result-awareness")
    .doc(searchId)
    .get();

  const data = doc.data();

  const parsedData: Awareness.Data = {
    pieChart: {},
    lineChart: [],
  };

  const mainBrand = brands[0];

  DATE_RANGES.forEach(({ value: range }) => {
    const rangeData = data?.pieChart?.[range];

    const mainBrandValue = rangeData?.values[mainBrand?.id!];
    if (typeof mainBrandValue !== "number") return;

    const values = [
      {
        brandName: mainBrand!.name,
        value: +mainBrandValue.toFixed(2),
      },
    ];

    brands.slice(1).forEach((brand) => {
      const brandValue = rangeData?.values[brand.id];
      if (typeof brandValue !== "number") return;

      values.push({
        brandName: brand.name,
        value: +brandValue.toFixed(2),
      });
    });

    parsedData.pieChart[range] = {
      startDate: rangeData?.startDate || "",
      endDate: rangeData?.endDate || "",
      values,
    };
  });

  const lineChartData = data?.lineChart;

  if (Array.isArray(lineChartData)) {
    lineChartData.forEach((item) => {
      const date = item?.date;
      if (!Date.parse(date)) return;

      const values: { [brandName: string]: number } = {};

      brands.forEach(({ id, name }) => {
        const brandValue = item?.values?.[id];
        if (typeof brandValue !== "number") return;

        values[name] = brandValue;
      });
      if (!Object.keys(values).length) return;

      parsedData.lineChart.push({
        date,
        values,
      });
    });
  }

  return {
    ...parsedData,
    lineChart: getLineChartDataForOneYear(parsedData.lineChart),
  };
}

export async function getReputationIndexWidget(
  searchId: string,
  brands: Store.Brands
): Promise<Store.Widgets["reputationIndex"]> {
  const parsedData: Store.Widgets["reputationIndex"] = [];

  const doc = await firestore()
    .collection("result-reputation-index")
    .doc(searchId)
    .get();

  const data = doc.data();

  if (data) {
    const labels = reputationParameters.map(({ name }) => ({
      name,
      key: name.toLowerCase(),
    }));

    brands.forEach(({ id, name }) => {
      const parsedBrandData: Store.Widgets["reputationIndex"][number]["brandData"] = {};

      // LINECHART
      const parsedLineChartData: Store.Widgets["reputationIndex"][number]["brandData"]["lineChart"] = [];

      const lineChartData = data[id]?.lineChart;

      if (Array.isArray(lineChartData)) {
        lineChartData.forEach((item) => {
          if (isNaN(Date.parse(item?.date))) return;

          const values: { [label: string]: number } = {};
          labels.forEach(({ name, key }) => {
            const value = item?.values?.[key];

            if (!Number.isFinite(value)) return;

            values[name] = value;
          });

          if (!Object.keys(values).length) return;

          parsedLineChartData.push({
            date: item.date,
            values,
          });
        });
      }

      const oneYearData = getLineChartDataForOneYear(parsedLineChartData);

      if (parsedLineChartData.length) {
        parsedBrandData.lineChart = oneYearData;
      }

      // PIECHART
      const brandPieChartData = data[id]?.pieChart;

      const startDate = !isNaN(Date.parse(brandPieChartData?.startDate))
        ? brandPieChartData.startDate
        : undefined;
      const endDate = !isNaN(Date.parse(brandPieChartData?.endDate))
        ? brandPieChartData.endDate
        : undefined;

      const values: { [label: string]: number } = {};

      labels.forEach(({ name, key }) => {
        const value = brandPieChartData?.values?.[key];

        if (!Number.isFinite(value)) return;

        values[name] = value;
      });

      if (startDate && endDate && Object.keys(values).length) {
        parsedBrandData.pieChart = {
          startDate,
          endDate,
          values,
        };
      }

      if (Object.keys(parsedBrandData).length) {
        parsedData.push({
          brandName: name,
          brandData: parsedBrandData,
        });
      }
    });
  }

  return parsedData;
}

export async function getReputationSentimentWidget(
  searchId: string,
  brands: Store.Brands
): Promise<Store.Widgets["reputationSentiment"]> {
  const parsedData: Store.Widgets["reputationSentiment"] = [];

  const doc = await firestore()
    .collection("result-reputation-sentiment")
    .doc(searchId)
    .get();

  const data = doc.data();

  if (data) {
    const labels = reputationParameters.map(({ name }) => ({
      name,
      key: name.toLowerCase(),
    }));

    brands.forEach(({ id, name }) => {
      const parsedBrandData: Store.Widgets["reputationSentiment"][number]["brandData"] = {};

      // LINECHART
      const parsedLineChartData: Store.Widgets["reputationSentiment"][number]["brandData"]["lineChart"] = [];

      const lineChartData = data[id]?.lineChart;
      if (Array.isArray(lineChartData)) {
        lineChartData.forEach((item) => {
          if (isNaN(Date.parse(item?.date))) return;

          const values: {
            [label: string]: {
              positive: number;
              negative: number;
              neutral: number;
            };
          } = {};
          labels.forEach(({ name, key }) => {
            const positive = item.values?.[key]?.positive;
            const negative = item.values?.[key]?.negative;
            const neutral = item.values?.[key]?.neutral;

            if (
              Number.isFinite(positive) &&
              Number.isFinite(negative) &&
              Number.isFinite(neutral)
            ) {
              values[name] = {
                positive,
                negative,
                neutral,
              };
            }
          });

          if (!Object.keys(values).length) return;

          parsedLineChartData.push({
            date: item.date,
            values,
          });
        });

        const oneYearData = getLineChartDataForOneYear(parsedLineChartData);

        if (oneYearData.length) {
          parsedBrandData.lineChart = oneYearData;
        }
      }

      // PIECHART
      const parsedPieChartData: Store.Widgets["reputationSentiment"][number]["brandData"]["pieChart"] = {};

      const pieChartData = data[id]?.pieChart;
      if (pieChartData) {
        DATE_RANGES.forEach(({ value: range }) => {
          const rangeData = pieChartData[range];
          if (!rangeData) return;

          const startDate = !isNaN(Date.parse(rangeData.startDate))
            ? rangeData.startDate
            : undefined;
          const endDate = !isNaN(Date.parse(rangeData.endDate))
            ? rangeData.endDate
            : undefined;

          const values: {
            [label: string]: {
              positive: number;
              negative: number;
              neutral: number;
            };
          } = {};
          labels.forEach(({ name, key }) => {
            const positive = rangeData.values?.[key]?.positive;
            const negative = rangeData.values?.[key]?.negative;
            const neutral = rangeData.values?.[key]?.neutral;

            if (
              Number.isFinite(positive) &&
              positive >= 0 &&
              Number.isFinite(negative) &&
              negative >= 0 &&
              Number.isFinite(neutral) &&
              neutral >= 0
            ) {
              values[name] = {
                positive,
                negative,
                neutral,
              };
            }
          });

          if (startDate && endDate && Object.keys(values).length) {
            parsedPieChartData[range] = {
              startDate,
              endDate,
              values,
            };
          }
        });

        if (Object.keys(parsedPieChartData).length) {
          parsedBrandData.pieChart = parsedPieChartData;
        }
      }

      if (Object.keys(parsedBrandData).length) {
        parsedData.push({
          brandName: name,
          brandData: parsedBrandData,
        });
      }
    });
  }

  return parsedData;
}

