export async function getDataScoreWidget(
  searchId: string
): Promise<Store.Widgets["dataScore"]> {
  const doc = await firestore()
    .collection("result-robustness-score")
    .doc(searchId)
    .get();

  const data = doc.data();

  if (!data) return initialState.dataScore;

  return {
    total: {
      robustness: {
        percentage: normalizeNumber(
          parseInteger(data.total?.robustness?.percentage) || 0,
          100,
          0
        ),
      },
      socialNetworks: {
        percentage: parseInteger(data.total?.socialNetworks?.percentage) || 0,
        quantity: parseInteger(data.total?.socialNetworks?.quantity) || 0,
        links:
          parseNonEmptyStringsArray(data.total?.socialNetworks?.links) || [],
      },
      blogs: {
        percentage: parseInteger(data.total?.blogs?.percentage) || 0,
        quantity: parseInteger(data.total?.blogs?.quantity) || 0,
        links: parseNonEmptyStringsArray(data.total?.blogs?.links) || [],
      },
      newsAndEditorial: {
        percentage: parseInteger(data.total?.newsAndEditorial?.percentage) || 0,
        quantity: parseInteger(data.total?.newsAndEditorial?.quantity) || 0,
        links:
          parseNonEmptyStringsArray(data.total?.newsAndEditorial?.links) || [],
      },
      forums: {
        percentage: parseInteger(data.total?.forums?.percentage) || 0,
        quantity: parseInteger(data.total?.forums?.quantity) || 0,
        links: parseNonEmptyStringsArray(data.total?.forums?.links) || [],
      },
      search: {
        percentage: parseInteger(data.total?.search?.percentage) || 0,
      },
      videos: {
        percentage: parseInteger(data.total?.videos?.percentage) || 0,
        quantity: parseInteger(data.total?.videos?.quantity) || 0,
        links: parseNonEmptyStringsArray(data.total?.videos?.links) || [],
      },
      opinionPolls: {
        percentage: parseInteger(data.total?.opinionPolls?.percentage) || 0,
      },
      productReviews: {
        percentage: parseInteger(data.total?.productReviews?.percentage) || 0,
        quantity: parseInteger(data.total?.productReviews?.quantity) || 0,
        links:
          parseNonEmptyStringsArray(data.total?.productReviews?.links) || [],
      },
    },
    timeline: Array.isArray(data.timeline)
      ? data.timeline.reduce((acc, item) => {
          const date = !isNaN(Date.parse(item.date)) ? item.date : undefined;

          const values = Object.entries(item.values).reduce(
            (acc, [dataType, percentage]) => {
              if (
                dataType in initialState.dataScore.total &&
                Number.isInteger(percentage)
              )
                acc.push([dataType, percentage as number]);

              return acc;
            },
            [] as [string, number][]
          );

          if (date && values.length)
            acc.push({
              date,
              values: Object.fromEntries(values),
            });

          return acc;
        }, [])
      : [],
    startDate: !isNaN(Date.parse(data.startDate)) ? data.startDate : undefined,
    endDate: !isNaN(Date.parse(data.endDate)) ? data.endDate : undefined,
  };
}

export async function getFunnelWidget(
  searchId: string,
  brands: Store.Brands
): Promise<Store.Widgets["funnel"]> {
  const parsedData: Store.Widgets["funnel"] = {};

  const doc = await firestore().collection("result-funnel").doc(searchId).get();

  const data = doc.data();

  const values = data?.values;

  if (values) {
    const mainBrand = brands[0];

    DATE_RANGES.forEach(({ value: range }) => {
      const rangeData = values[range];
      if (!rangeData) return;

      if (isNaN(Date.parse(rangeData.startDate))) return;

      if (isNaN(Date.parse(rangeData.endDate))) return;

      const mainBrandValues = parseValues(rangeData.values?.[mainBrand?.id!]);
      if (!mainBrandValues) return;

      const brandValues = [
        {
          brandName: mainBrand!.name,
          values: mainBrandValues,
        },
      ];

      brands.slice(1).forEach((brand) => {
        const values = parseValues(rangeData.values?.[brand.id]);
        if (!values) return;

        brandValues.push({
          brandName: brand.name,
          values,
        });
      });

      parsedData[range] = {
        startDate: rangeData.startDate,
        endDate: rangeData.endDate,
        brandValues,
      };
    });
  }

  function parseValues(brandData: any) {
    if (!brandData) return;

    const labels = [
      "interest",
      "consideration",
      "preference",
      "purchase intent",
    ] as const;

    const values = labels.reduce(
      (acc, label, i) => {
        const value = brandData["section" + (i + 1)];

        if (Number.isInteger(value)) {
          acc.push({
            label,
            value,
          });
        }

        return acc;
      },
      [] as {
        label: typeof labels[number];
        value: number;
      }[]
    );

    if (values.length === labels.length) return values;
  }

  return parsedData;
}

export const getWidget = async (
  searchId: string,
  widgetName: keyof Omit<
    Store.Widgets,
    | "shareOfEarnedMedia"
    | "shareOfSearch"
    | "awareness"
    | "dataScore"
    | "reputationIndex"
    | "reputationSentiment"
    | "reputationDistribution"
    | "esgIndex"
    | "esgSentiment"
    | "esgDistribution"
    | "funnel"
    | "businessDrivers"
    | "competitiveness"
  >
) => {
  type DateRange = "oneMonth" | "twoMonths" | "sixMonths" | "oneYear";

  const db = firestore();

  // const START_DATES = {
  //   oneMonth: formatDate2(getDateWithMonthOffset(-1)),
  //   twoMonths: formatDate2(getDateWithMonthOffset(-2)),
  //   sixMonths: formatDate2(getDateWithMonthOffset(-6)),
  //   oneYear: formatDate2(getDateWithMonthOffset(-12)),
  // } as const;

  const widgets = {
    attitude: {
      collectionName: "result-attitude",
      dataHandler: (data: firestore.DocumentData): Attitude.Data => {
        const DATE_RANGES = {
          oneMonth: {
            dataInterval: "weeklyData",
            dataHandler: (data: any[]) => data.slice(-5),
          },
          twoMonths: {
            dataInterval: "weeklyData",
            dataHandler: (data: any[]) => data.slice(-9),
          },
          sixMonths: {
            dataInterval: "weeklyData",
            dataHandler: (data: any[]) => data.slice(-26),
          },
          oneYear: {
            dataInterval: "monthlyData",
            dataHandler: (data: any[]) => data.slice(-12),
          },
        } as const;

        function getValues(initialValues: any) {
          const values: {
            [brandId: string]: Attitude.BrandValue;
          } = {};
          if (typeof initialValues !== "string") {
            initialValues = { ...initialValues };
          }
          for (const brandId in initialValues) {
            const { positive, negative, neutral } = initialValues[brandId];
            if (
              Number.isFinite(positive) &&
              Number.isFinite(negative) &&
              Number.isFinite(neutral) &&
              Math.max(positive, negative, neutral) > 0
            ) {
              values[brandId] = {
                Positive: positive,
                Negative: negative,
                Neutral: neutral,
              };
            }
          }
          return values;
        }

        function getPieChartData(
          dateRange: DateRange
        ): Attitude.PieChartDataItem | undefined {
          const values = getValues(
            data.values?.pieChartData?.[dateRange]?.values
          );
          if (!Object.keys(values).length) return;
          return {
            startDate: data.values?.pieChartData?.[dateRange]?.startDate || "",
            endDate: data.values?.pieChartData?.[dateRange]?.endDate || "",
            // startDate: START_DATES[dateRange],
            // endDate: formatDate2(new Date()),
            values,
          };
        }

        function getLineChartData(
          dateRange: keyof typeof DATE_RANGES
        ): Attitude.LineChartDataItem[] | undefined {
          const lineChartDataItems: Attitude.LineChartDataItem[] = [];
          const initialLineChartDataItems =
            data.values?.lineChartData?.[DATE_RANGES[dateRange].dataInterval];
          if (Array.isArray(initialLineChartDataItems)) {
            initialLineChartDataItems.sort((a, b) => {
              const dateA = parseDate(a.date);
              const dateB = parseDate(b.date);
              if (dateA && dateB) return dateA.getTime() - dateB.getTime();
              return 0;
            });

            // const lastDate = initialLineChartDataItems[initialLineChartDataItems.length - 1].date;
            // const timeOffset = new Date().getTime() - new Date(lastDate).getTime();
            DATE_RANGES[dateRange]
              .dataHandler(initialLineChartDataItems)
              .forEach(({ date, values: initialValues }) => {
                const values = getValues(initialValues);
                if (!Object.keys(values).length) return;
                const dateTime = new Date(date).getTime();
                // const modifiedDateTime = new Date(dateTime + timeOffset).getTime();
                lineChartDataItems.push({
                  date: formatDate2(dateTime),
                  values,
                });
              });
          }
          return lineChartDataItems.length ? lineChartDataItems : undefined;
        }

        return {
          pieChartData: {
            oneMonth: getPieChartData("oneMonth"),
            twoMonths: getPieChartData("twoMonths"),
            sixMonths: getPieChartData("sixMonths"),
            oneYear: getPieChartData("oneYear"),
          },
          lineChartData: {
            oneMonth: getLineChartData("oneMonth"),
            twoMonths: getLineChartData("twoMonths"),
            sixMonths: getLineChartData("sixMonths"),
            oneYear: getLineChartData("oneYear"),
          },
        };
      },
    },
    marketingMix: {
      collectionName: "result-marketing-mix",
      dataHandler: (data: firestore.DocumentData): BusinessDriversData => {
        const modifyValue = (value: string) => {
          const impact = data[value]?.impact;
          return Number.isFinite(impact) ? +impact.toFixed() : 0;
        };
        return {
          Product: modifyValue("prd"),
          Price: modifyValue("prc"),
          Service: modifyValue("ppl"),
          Preference: modifyValue("prf"),
          Offer: modifyValue("prp"),
          Communication: modifyValue("prm"),
        };
      },
    },
    keyAreas: {
      collectionName: "result-marketing-mix",
      dataHandler: (data: firestore.DocumentData): KeyAreasData => {
        const impactValues = [
          data.prd.impact,
          data.prc.impact,
          data.ppl.impact,
          data.prf.impact,
          data.prp.impact,
          data.prm.impact,
        ];
        const potentialValues = [
          data.prd.potential,
          data.prc.potential,
          data.ppl.potential,
          data.prf.potential,
          data.prp.potential,
          data.prm.potential,
        ];
        function getValueRelativePercentage(value: number, values: number[]) {
          const PERCENTAGE_BORDER = 15;
          let minValue = Math.min(...values);
          let maxValue = Math.max(...values);
          if (minValue === maxValue) {
            minValue--;
            maxValue++;
          }
          const result =
            PERCENTAGE_BORDER +
            ((value - minValue) * (100 - PERCENTAGE_BORDER * 2)) /
              (maxValue - minValue);
          return +result.toFixed();
        }

        return [
          {
            label: "Product",
            key: "prd",
          },
          {
            label: "Price",
            key: "prc",
          },
          {
            label: "Service",
            key: "ppl",
          },
          {
            label: "Preference",
            key: "prf",
          },
          {
            label: "Offer",
            key: "prp",
          },
          {
            label: "Communication",
            key: "prm",
          },
        ]
          .sort(
            (a, b) =>
              data[b.key].impact +
              data[b.key].potential -
              (data[a.key].impact + data[a.key].potential)
          )
          .slice(0, 3)
          .map(({ label, key }) => ({
            label,
            values: {
              impact: getValueRelativePercentage(
                data[key].impact,
                impactValues
              ),
              potential: getValueRelativePercentage(
                data[key].potential,
                potentialValues
              ),
            },
          }));
      },
    },
    generalAttributes: {
      collectionName: "result-general-attributes",
      dataHandler: (data: firestore.DocumentData): GeneralAttributes.Data => {
        function getData(
          dateRange: DateRange
        ): GeneralAttributes.Item | undefined {
          const itemData: GeneralAttributes.ItemDataValue[] = [];
          const initialItemData = data[dateRange]?.data;
          if (Array.isArray(initialItemData)) {
            initialItemData.forEach(
              ({ attribute, values, descriptor: initialWords }) => {
                const words: string[] = [];
                if (Array.isArray(initialWords)) {
                  initialWords.forEach((initialWord) => {
                    if (initialWord && typeof initialWord === "string")
                      words.push(initialWord.toUpperCase());
                  });
                }
                itemData.push({
                  attribute: attribute.toUpperCase(),
                  values: typeof values !== "string" ? { ...values } : {},
                  words: words.slice(0, 3),
                });
              }
            );
          }
          if (itemData.length < 4) return;
          return {
            startDate: data[dateRange]?.startDate || "",
            endDate: data[dateRange]?.endDate || "",
            // startDate: START_DATES[dateRange],
            // endDate: formatDate2(new Date()),
            data: itemData,
          };
        }
        return {
          oneMonth: getData("oneMonth"),
          twoMonths: getData("twoMonths"),
          sixMonths: getData("sixMonths"),
          oneYear: getData("oneYear"),
        };
      },
    },
    positionVsCompetition: {
      collectionName: "result-position-vs-competition",
      dataHandler: (
        data: firestore.DocumentData
      ): PositionVsCompetition.Data => {
        function normalizeValue(number: number) {
          if (number < -100) return -100;
          if (number > 100) return 100;
          return number;
        }
        function getData(
          dateRange: DateRange
        ): PositionVsCompetition.Item | undefined {
          const initialAttributes = data[dateRange]?.attributes;
          const attributes: string[] = [];
          if (Array.isArray(initialAttributes)) {
            initialAttributes.forEach((attribute) => {
              if (attribute && typeof attribute === "string") {
                const modifiedAttribute = attribute
                  .replace(/People/i, "Service")
                  .replace(/Proposal/i, "Offer")
                  .replace(/Promotion/i, "Communication");
                attributes.push(modifiedAttribute.toUpperCase());
              }
            });
          }
          const values: {
            [brandId: string]: PositionVsCompetition.BrandValue;
          } = {};
          let initialValues = data[dateRange]?.values;
          if (typeof initialValues !== "string") {
            initialValues = { ...initialValues };
          }
          for (const brandId in initialValues) {
            const initialBrandValue = initialValues[brandId];
            const initialX = initialBrandValue.x;
            const initialY = initialBrandValue.y;
            if (Number.isFinite(initialX) && Number.isFinite(initialY)) {
              values[brandId] = {
                x: normalizeValue(initialX),
                y: normalizeValue(initialY),
              };
            }
          }
          if (attributes.length < 4 || !Object.keys(values).length) return;
          return {
            startDate: data[dateRange]?.startDate || "",
            endDate: data[dateRange]?.endDate || "",
            // startDate: START_DATES[dateRange],
            // endDate: formatDate2(new Date()),
            attributes: attributes.slice(0, 4),
            values,
          };
        }
        return {
          oneMonth: getData("oneMonth"),
          twoMonths: getData("twoMonths"),
          sixMonths: getData("sixMonths"),
          oneYear: getData("oneYear"),
        };
      },
    },
    sentimentDrivers: {
      collectionName: "result-trending-words",
      dataHandler: (data: firestore.DocumentData): SentimentDrivers.Data => {
        function getModifiedValues(values: any) {
          const modifiedValues: SentimentDrivers.Value[] = [];
          if (Array.isArray(values)) {
            values.forEach(({ word, relatedWords }: any) => {
              if (!(word && typeof word === "string")) return;
              if (!Array.isArray(relatedWords)) return;

              const modifiedRelatedWords: string[] = relatedWords.filter(
                (x) => x && typeof x === "string"
              );
              // .slice(0, 3)
              // .map(x => x.toUpperCase());

              if (!modifiedRelatedWords.length) return;

              modifiedValues.push({
                word, //: word.toUpperCase(),
                relatedWords: modifiedRelatedWords,
              });
            });
          }

          return modifiedValues.slice(0, 3);
        }

        function getData(
          dateRange: DateRange
        ): SentimentDrivers.Item | undefined {
          let initialValues = data[dateRange]?.values;
          if (typeof initialValues !== "string") {
            initialValues = { ...initialValues };
          }

          const values: { [brandId: string]: SentimentDrivers.BrandValue } = {};

          Object.entries(initialValues).forEach(
            ([brand, value]: [any, any]) => {
              const positiveModifiedValues = getModifiedValues(value.positive);
              const negativeModifiedValues = getModifiedValues(value.negative);

              if (
                positiveModifiedValues.length ||
                negativeModifiedValues.length
              ) {
                values[brand] = {
                  positive: positiveModifiedValues.length
                    ? positiveModifiedValues
                    : undefined,
                  negative: negativeModifiedValues.length
                    ? negativeModifiedValues
                    : undefined,
                };
              }
            }
          );

          if (!Object.keys(values).length) return;

          return {
            startDate: data[dateRange]?.startDate || "",
            endDate: data[dateRange]?.endDate || "",
            // startDate: START_DATES[dateRange],
            // endDate: formatDate2(new Date()),
            values,
          };
        }
        return {
          oneMonth: getData("oneMonth"),
          twoMonths: getData("twoMonths"),
          sixMonths: getData("sixMonths"),
          oneYear: getData("oneYear"),
        };
      },
    },
  };
  const docRef = db
    .collection(widgets[widgetName].collectionName)
    .doc(searchId);
  const doc = await docRef.get();
  if (doc.exists) {
    const data = doc.data();
    if (data) {
      return widgets[widgetName].dataHandler(data);
      // dispatch(setWidget(widgetName, widgets[widgetName].dataHandler(data)));
    }
  } else {
    // dispatch(setWidget(widgetName, initialState[widgetName]));
  }
  return initialState[widgetName];
};
