import { CSSProperties } from "react";
import { useDrag } from "react-dnd";

interface DragProps {
  id: string;
  type: string;
}

export const withDrag = <T extends object & { style?: CSSProperties }>(
  Component: React.ComponentType<T>
) => ({ id, type, ...props }: T & DragProps) => {
  const [{ opacity }, drag] = useDrag({
    item: { id, type },
    collect: (monitor) => ({
      opacity: monitor.isDragging() ? 0.4 : 1,
    }),
  });

  return (
    <Component
      ref={drag}
      style={{ ...props.style, opacity }}
      {...(props as T)}
    />
  );
};
