import actionTypes from "../actionTypes";
import { postUser, postSurveyRequest } from "../api";
import { executeCapterra } from "../../utils";
import firebase from "firebase/app";
import "firebase/auth";
import "firebase/firestore";
import TagManager from "react-gtm-module";

// MUTATIONS
export const setUserData = (userData: Partial<Store.User>) => {
  return {
    type: actionTypes.SET_USER_DATA,
    userData,
  };
};

export const removeUserData = () => {
  return {
    type: actionTypes.REMOVE_USER_DATA,
  };
};

// ACTIONS
export const setAuthObserver = () => {
  return async (dispatch: Function): Promise<void> => {
    await new Promise<void>((resolve) => {
      firebase.auth().onAuthStateChanged((user) => {
        if (user) {
          try {
            TagManager.dataLayer({
              dataLayer: {
                event: "Log-in",
                category: "Competitive Compass",
                action: "Log-in",
              },
            });
          } catch (error) {
            console.log(error);
          }
          dispatch(
            setUserData({
              id: user.uid,
              email: user.email ? user.email : "",
            })
          );
        } else {
          dispatch({
            type: "user/loggedOut",
          });
        }
        resolve();
      });
    });
  };
};

export const signUp = (
  firstName: string,
  lastName: string,
  email: string,
  password: string,
  company?: string
): Function => {
  return async () => {
    await postUser({ firstName, lastName, email, password, company });
    if (
      process.env.NODE_ENV === "production" &&
      process.env.REACT_APP_ENV === "prod"
    ) {
      try {
        TagManager.dataLayer({
          dataLayer: {
            event: "Sign-up",
            category: "Competitive Compass",
            action: "Sign-up",
          },
        });
        executeCapterra();
      } catch (error) {
        console.log(error);
      }
    }
  };
};

export const logIn = (
  email: string,
  password: string,
  isRemembered: boolean | undefined = true
): Function => {
  const persistence = isRemembered ? "local" : "session";

  return async (): Promise<void> => {
    await firebase
      .auth()
      .setPersistence(persistence)
      .then(() => {
        return firebase.auth().signInWithEmailAndPassword(email, password);
      })
      .catch((error) => {
        throw error;
      });
  };
};

export const reAuthenticate = (password: string): Function => {
  return async (): Promise<void> => {
    const user = firebase.auth().currentUser;
    if (user && user.email) {
      const credential = firebase.auth.EmailAuthProvider.credential(
        user.email,
        password
      );
      await user.reauthenticateWithCredential(credential).catch((error) => {
        throw error.message;
      });
    }
  };
};

export const updateEmail = (email: string): Function => {
  return async (dispatch: Function): Promise<void> => {
    const user = firebase.auth().currentUser;
    if (user) {
      await user.updateEmail(email).catch((error) => {
        throw error.message;
      });
      dispatch(setUserData({ email }));
    }
  };
};

export const updatePassword = (password: string): Function => {
  return async (): Promise<void> => {
    const user = firebase.auth().currentUser;
    if (user) {
      await user.updatePassword(password).catch((error) => {
        throw error.message;
      });
    }
  };
};

export const resetPassword = (email: string): Function => {
  return async (): Promise<void> => {
    await firebase
      .auth()
      .sendPasswordResetEmail(email)
      .catch((error) => {
        console.dir(error);
        throw error.message;
      });
  };
};

export const logOut = (): Function => {
  return async (): Promise<void> => {
    await firebase.auth().signOut();
  };
};

