export const updateUserProfileSettings = (settings: {
  avatar?: string;
  company?: string;
  title?: string;
  branch?: string;
  market?: string;
  phone?: string;
}): Function => {
  return async (dispatch: Function, getState: Function): Promise<void> => {
    const { user } = getState();
    const db = firebase.firestore();
    const docRef = db.collection("userProfiles").doc(user.id);
    await docRef.set({ settings }, { merge: true });
    dispatch(setUserData(settings));
  };
};

export const updateIsUserReportReceiver = (): Function => {
  return async (dispatch: Function, getState: Function): Promise<void> => {
    const { user } = getState();
    const db = firebase.firestore();
    const docRef = db.collection("userProfiles").doc(user.id);
    await docRef.set(
      { monthlyReportReciever: !user.isReportReceiver },
      { merge: true }
    );
    dispatch(
      setUserData({
        isReportReceiver: !user.isReportReceiver,
      })
    );
  };
};

export const requestSurvey = (searchId: string): Function => {
  return async (dispatch: Function, getState: Function) => {
    const { user } = getState() as Store.State;
    await postSurveyRequest(user.id, searchId);
    dispatch(
      setUserData({
        surveyRequestSearchIds: [...user.surveyRequestSearchIds, searchId],
      })
    );
  };
};

export const hardReloadAction = () => {
  return async (dispatch: Function, getState: Function) => {
    const { user } = getState();

    const db = firebase.firestore();
    const docRef = db.collection("userProfiles").doc(user.id);

    const subscription = docRef.onSnapshot((snap: any) => {
      const whetherToReboot = snap.data().hardReload;

      if (whetherToReboot) {
        if (whetherToReboot.whetherItNeedsUpdate) {
          const newData = {
            whetherItNeedsUpdate: false,
            lastUpdate: new Date().toISOString(),
          };

          db.collection("userProfiles")
            .doc(user.id)
            .set({ hardReload: newData }, { merge: true })
            .then(() => {
              window.location.reload(true);
            });
        }
      }
    });

    return subscription;
  };
};
