import actionTypes from "../actionTypes";
import {
  getSearches,
  postSearch,
  putSearch,
  deleteSearch,
  writeSearchExcludedSuggestions,
} from "../api";
import TagManager from "react-gtm-module";

// MUTATIONS
export const setSearches = (searches: Store.Searches) => {
  return {
    type: actionTypes.SET_SEARCHES,
    searches,
  };
};

// ACTIONS
export const readSearches = (companyId?: string) => {
  return async (dispatch: Function, getState: Function) => {
    if (!companyId) {
      const { company } = getState() as Store.State;
      companyId = company.id;
    }

    const { data } = await getSearches(companyId);

    const searches: Store.Searches = [];

    if (Array.isArray(data)) {
      data.forEach((search) => {
        const competitors: Store.Search["brands"]["competitors"] = [];
        const suggestions: Store.Search["suggestions"] = {};

        if (Array.isArray(search.brands)) {
          search.brands.forEach(
            (
              {
                name: brandName,
                include: included,
                exclude: excluded,
                keywords: selected,
              }: any,
              i: number
            ) => {
              if (typeof brandName !== "string") return;

              if (i > 0) {
                competitors.push(brandName);
              }

              suggestions[brandName] = {
                included: Array.isArray(included)
                  ? included.filter((x: any) => x && typeof x === "string")
                  : [],
                excluded: Array.isArray(excluded)
                  ? excluded.filter((x: any) => x && typeof x === "string")
                  : [],
                selected: Array.isArray(selected)
                  ? selected.filter((x: any) => x && typeof x === "string")
                  : [],
              };
            }
          );
        }

        searches.push({
          id: String(search.brandId ?? ""),
          name: search.searchName || "",
          brands: {
            main: search.mainBrand || "",
            competitors,
          },
          industry: {
            id: search.industryId || "",
            name: search.industryName || "",
          },
          market: {
            code: search.marketId || "",
          },
          suggestions,
          owner: {
            id: String(search.ownerId ?? ""),
          },
          isPending: search.status !== "FINISHED",
        });
      });
    }

    dispatch(setSearches(searches));

    return searches;
  };
};

export const createSearch = ({
  userId,
  name,
  mainBrand,
  competitors,
  industryId,
  marketCode,
  suggestions,
}: {
  userId: string;
  name: string;
  mainBrand: string;
  competitors: string[];
  industryId: string;
  marketCode: string;
  suggestions: Store.Search["suggestions"];
}): Function => {
  return async (
    dispatch: Function,
    getState: Function
  ): Promise<{
    searchId?: string;
  }> => {
    const { searches, industries } = getState() as Store.State & RootState;

    const industry = industries.entities[industryId];

    const { data } = await postSearch({
      userId,
      name,
      mainBrand,
      competitors,
      industryId,
      industry,
      marketCode,
      suggestions,
    });

    if (!searches.length) {
      TagManager.dataLayer({
        dataLayer: {
          event: "First search",
          category: "Competitive Compass",
          action: "First search",
        },
      });
    }

    dispatch(
      setSearches([
        ...searches,
        {
          id: data.brandId,
          name,
          brands: {
            main: mainBrand,
            competitors: competitors,
          },
          industry: {
            id: industryId,
            name: industry?.label || "",
          },
          market: {
            code: marketCode,
          },
          suggestions,
          owner: {
            id: userId,
          },
          isPending: true,
        },
      ])
    );

    return {
      searchId: data.brandId,
    };
  };
};

export const editSearch = (
  searchId: string,
  {
    name,
    mainBrand,
    competitors,
    industryId,
    marketCode,
    suggestions,
  }: {
    name: string;
    mainBrand: string;
    competitors: string[];
    industryId: string;
    marketCode: string;
    suggestions: Store.Search["suggestions"];
  }
): Function => {
  return async (dispatch: Function, getState: Function) => {
    const { user, searches, industries } = getState() as Store.State &
      RootState;

    const industry = industries.entities[industryId];

    await putSearch(searchId, {
      userId: user.id,
      name,
      mainBrand,
      competitors,
      industryId,
      industry,
      marketCode,
      suggestions,
    });

    dispatch(
      setSearches(
        searches.map((search) =>
          search.id === searchId
            ? {
                id: searchId,
                name,
                brands: {
                  main: mainBrand,
                  competitors: competitors,
                },
                industry: {
                  id: industryId,
                  name: industry?.label || "",
                },
                market: {
                  code: marketCode,
                },
                suggestions,
                owner: {
                  id: user.id,
                },
                isPending: true,
              }
            : search
        )
      )
    );
  };
};

export const removeSearch = (searchId: string): Function => {
  return async (dispatch: Function, getState: Function) => {
    const { user, searches } = getState() as Store.State;
    await deleteSearch(user.id, searchId);
    dispatch(setSearches(searches.filter(({ id }) => id !== searchId)));
  };
};

export const updateSearchExcludedSuggestions = (
  searchId: Store.Search["id"],
  excludedSuggestions: string[]
): Function => {
  return async (dispatch: Function, getState: Function) => {
    const { searches, brands } = getState() as Store.State;
    dispatch(
      setSearches(
        searches.map((search) =>
          search.id === searchId
            ? {
                ...search,
                suggestions: {
                  ...search.suggestions,
                  [brands[0]!.name]: {
                    ...search.suggestions[brands[0]!.name],
                    excluded: excludedSuggestions,
                  },
                },

                // keywords: { ...search.keywords, excluded: keywords }
              }
            : search
        )
      )
    );
    await writeSearchExcludedSuggestions(
      searchId,
      brands[0]!.id,
      excludedSuggestions
    );
  };
};
