import { FC, useContext, useState, useMemo } from "react";
import styles from "./FirstSearchModal.module.scss";
import { Label, Input, Select, FadeInImage } from "../../components";
import { useHistory } from "react-router-dom";
import context from "../../context";
import { useSelector, useDispatch } from "react-redux";
import { selectAllMarketsAsInputOptions } from "../markets/marketsSlice";
import { selectAllIndustriesAsInputOptions } from "../industries/industriesSlice";
import { createSearch } from "../../store/actions";
import cx from "classnames";
import { useTemporaryErrors } from "../../hooks";
import { withError } from "../../hocs";
import { notify } from "react-notify-toast";

const InputWithError = withError(Input);
const SelectWithError = withError(Select);

export const FirstSearchModal: FC = () => {
  const { setIsGlobalPreloaderShown, setModalElement } = useContext(context);

  const history = useHistory();

  const markets = useSelector(selectAllMarketsAsInputOptions);

  const industries = useSelector(selectAllIndustriesAsInputOptions);

  const userId = useSelector(({ user }: Store.State) => user.id);

  const dispatch = useDispatch();

  const [currentViewIndex, setCurrentViewIndex] = useState(0);
  const [searchType, setSearchType] = useState<"easy" | "refined">();
  const [mainBrand, setMainBrand] = useState("");
  const [competitor1, setCompetitor1] = useState("");
  const [competitor2, setCompetitor2] = useState("");
  const [competitor3, setCompetitor3] = useState("");
  const [competitor4, setCompetitor4] = useState("");
  const [marketCode, setMarketCode] = useState("");
  const [industryId, setIndustryId] = useState("");
  const [searchName, setSearchName] = useState("");

  const finalSearchData = useMemo(() => {
    return {
      mainBrand,
      competitors: [competitor1, competitor2, competitor3, competitor4].filter(
        (x) => x
      ),
      industryId,
      marketCode,
      name: searchName.trim(),
      suggestions: {},
    };
  }, [
    mainBrand,
    competitor1,
    competitor2,
    competitor3,
    competitor4,
    industryId,
    marketCode,
    searchName,
  ]);

  const { errors, setErrors } = useTemporaryErrors(3000);

  function onNextClicked() {
    const validationErrors: typeof errors = {};

    switch (currentViewIndex) {
      case 1:
        if (!mainBrand.trim()) validationErrors.mainBrand = "Required";
        break;
      case 2:
        const brands = [
          mainBrand,
          competitor1,
          competitor2,
          competitor3,
          competitor4,
        ].map((brand) => brand.trim().toLowerCase());
        if (!brands[1]) validationErrors.competitor1 = "Required";
        brands.slice(1).forEach((brand, i) => {
          if (brand && brands.slice(0, i + 1).includes(brand))
            validationErrors["competitor" + (i + 1)] =
              "This value is already entered";
        });
        break;
      case 3:
        if (!industryId) validationErrors.industryId = "Required";
        if (!marketCode) validationErrors.marketCode = "Required";

        if (searchType === "refined" && industryId && marketCode) {
          setModalElement(null);

          const {
            mainBrand,
            competitors,
            industryId,
            marketCode,
          } = finalSearchData;

          history.push(
            "/searches/new?stage=2" +
              `&mainBrand=${mainBrand}` +
              `&competitor1=${competitors[0]}` +
              `&competitor2=${competitors[1] ?? ""}` +
              `&competitor3=${competitors[2] ?? ""}` +
              `&competitor4=${competitors[3] ?? ""}` +
              `&industryId=${industryId}` +
              `&marketCode=${marketCode}`
          );
        }

        break;
      default:
        break;
    }

    if (Object.keys(validationErrors).length) {
      setErrors(validationErrors);
      return;
    }

    setCurrentViewIndex((prev) => prev + 1);
  }

  async function onSaveClicked() {
    if (!searchName.trim()) {
      setErrors({ searchName: "Required" });
      return;
    }

    setIsGlobalPreloaderShown(true);
    try {
      await dispatch(createSearch({ userId, ...finalSearchData }));

      setModalElement(null);
      history.push("/widget-library");
    } catch (error) {
      console.dir(error);
      notify.show("Oops, something went wrong", "error");
    }
    setIsGlobalPreloaderShown(false);
  }

  return (
    <div className={styles.firstSearchModal}>
      {!!currentViewIndex && (
        <button
          className={cx(styles.greyButton, styles.backButton)}
          onClick={() => setCurrentViewIndex((prev) => prev - 1)}
        >
          Go back
        </button>
      )}
      {currentViewIndex === 0 && (
        <div className={styles.view0}>
          <div className={styles.title}>
            Welcome! Lets create a search. You have two options
          </div>
          <div className={styles.tiles}>
            <div className={styles.greenTile}>
              <div>EASY SEARCH - Quick start</div>
              <FadeInImage className={styles.image} name="birdHipHop" />
              <button
                className={styles.greyButton}
                onClick={() => {
                  setSearchType("easy");
                  onNextClicked();
                }}
              >
                Next
              </button>
            </div>
            <div className={styles.goldTile}>
              <div>REFINED SEARCH</div>
              <FadeInImage className={styles.image} name="birdsScientists" />
              <button
                className={styles.greenButton}
                onClick={() => {
                  setSearchType("refined");
                  onNextClicked();
                }}
              >
                Next
              </button>
            </div>
          </div>
          <div className={styles.warning}>
            Remember, If you choose easy search now you can always refine your
            search later on.
          </div>
        </div>
      )}
      {currentViewIndex >= 1 && currentViewIndex <= 3 && (
        <div className={styles.viewFrom1To3}>
          <div className={styles.content}>
            <div className={styles.formArea}>
              <div className={styles.overview}>
                {currentViewIndex > 1 && <div>Brand/topic: {mainBrand}</div>}
                {currentViewIndex > 2 && (
                  <div>
                    Compared with: {finalSearchData.competitors.join(", ")}
                  </div>
                )}
              </div>
              <div className={styles.form}>
                <div>
                  {currentViewIndex === 1 && (
                    <>
                      <div className={styles.title}>
                        First of all, we need you to type in a brand or a topic
                        to track.
                      </div>
                      <div
                        className={styles.inputWrapper}
                        style={{ width: "100%" }}
                      >
                        <Label leftText="Main brand or topic to track" />
                        <InputWithError
                          className={styles.input}
                          value={mainBrand}
                          changeHandler={(value) => setMainBrand(value)}
                          error={errors.mainBrand}
                        />
                      </div>
                    </>
                  )}
                  {currentViewIndex === 2 && (
                    <>
                      <div className={styles.title}>
                        Then we need something to benchmark that with. We need
                        at least one, at most four.
                      </div>
                      <div className={styles.inputWrapper}>
                        <Label leftText="Compare with 1" />
                        <InputWithError
                          className={styles.input}
                          value={competitor1}
                          changeHandler={(value) => setCompetitor1(value)}
                          error={errors.competitor1}
                        />
                      </div>
                      <div className={styles.inputWrapper}>
                        <Label
                          leftText="Compare with 2"
                          rightText="(Optional)"
                        />
                        <InputWithError
                          className={styles.input}
                          value={competitor2}
                          changeHandler={(value) => setCompetitor2(value)}
                          error={errors.competitor2}
                        />
                      </div>
                      <div className={styles.inputWrapper}>
                        <Label
                          leftText="Compare with 3"
                          rightText="(Optional)"
                        />
                        <InputWithError
                          className={styles.input}
                          value={competitor3}
                          changeHandler={(value) => setCompetitor3(value)}
                          error={errors.competitor3}
                        />
                      </div>
                      <div className={styles.inputWrapper}>
                        <Label
                          leftText="Compare with 4"
                          rightText="(Optional)"
                        />
                        <InputWithError
                          className={styles.input}
                          value={competitor4}
                          changeHandler={(value) => setCompetitor4(value)}
                          error={errors.competitor4}
                        />
                      </div>
                    </>
                  )}
                  {currentViewIndex === 3 && (
                    <>
                      <div className={styles.title}>
                        And finally we need to know in what market and industry
                        we are talking about.
                      </div>
                      <div className={styles.inputWrapper}>
                        <Label leftText="Market" />
                        <SelectWithError
                          options={markets}
                          value={marketCode}
                          changeHandler={(value) => setMarketCode(value)}
                          error={errors.marketCode}
                        />
                      </div>
                      <div className={styles.inputWrapper}>
                        <Label leftText="Industry" />
                        <SelectWithError
                          options={industries}
                          value={industryId}
                          changeHandler={(value) => setIndustryId(value)}
                          error={errors.industryId}
                        />
                      </div>
                    </>
                  )}
                </div>
                <button className={styles.greenButton} onClick={onNextClicked}>
                  Next
                </button>
              </div>
            </div>
            <div className={styles.imageArea}>
              <FadeInImage className={styles.image} name="birdWaving" />
            </div>
          </div>
        </div>
      )}
      {currentViewIndex === 4 && (
        <div className={styles.view4}>
          <div className={styles.content}>
            <div className={styles.imageArea}>
              <FadeInImage className={styles.image} name="birdHipHop" />
            </div>
            <div className={styles.formArea}>
              <div className={styles.title}>
                Great! You can always access your data later on if you want to
                refine it. We will let you know as soon as data is ready for
                you.
              </div>
              <div className={styles.inputWrapper}>
                <div className={styles.label}>Save this search as:</div>
                <div className={styles.inputInnerWrapper}>
                  <InputWithError
                    className={styles.input}
                    value={searchName}
                    changeHandler={(value) => setSearchName(value)}
                    error={errors.searchName}
                  />
                </div>
                <button className={styles.greenButton} onClick={onSaveClicked}>
                  Save
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};
