import { useContext, useState, useEffect } from "react";
import styles from "./Login.module.scss";
import context from "../../../context";
import { Link, useHistory } from "react-router-dom";
import { connect } from "react-redux";
import * as actions from "../../../store/actions";
import { Input } from "../../../components";
import { notify } from "react-notify-toast";
import { useTemporaryErrors } from "../../../hooks";
import { WebsiteLogoVertical } from "../../../assets/icons";
import { withError } from "../../../hocs";

const InputWithError = withError(Input);

interface Props {
  logIn: typeof actions.logIn;
}

const Login = ({ logIn }: Props) => {
  const history = useHistory();

  const { setIsGlobalPreloaderShown } = useContext(context);

  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [isRemembered, setIsRemembered] = useState(
    () =>
      !localStorage["Telescope.isAuthRemembered"] ||
      localStorage["Telescope.isAuthRemembered"] === "true"
  );

  const { errors, setErrors } = useTemporaryErrors(3000);

  useEffect(() => {
    localStorage["Telescope.isAuthRemembered"] = isRemembered;
  }, [isRemembered]);

  function validate() {
    const validationErrors: typeof errors = {};
    if (!email.trim().length) validationErrors.email = "Required";
    if (!password.trim().length) validationErrors.password = "Required";
    return validationErrors;
  }

  async function handleSubmitForm() {
    const errors = validate();
    if (Object.keys(errors).length) {
      setErrors(errors);
      return;
    }

    setIsGlobalPreloaderShown(true);
    try {
      await logIn(email.trim(), password.trim(), isRemembered);
    } catch (error) {
      notify.show(error.message || "Oops, something went wrong", "error");
    }
    setIsGlobalPreloaderShown(false);
  }

  return (
    <div className={styles.login}>
      <div className={styles.logoWrapper}>
        <WebsiteLogoVertical className={styles.logo} />
      </div>
      <div className={styles.title}>Activate your Competitive Compass</div>
      <div className={styles.note}>
        30 day free trial. No credit card needed.
      </div>
      <form
        name="logIn"
        autoComplete="off"
        onSubmit={(e) => {
          e.preventDefault();
          handleSubmitForm();
        }}
      >
        <InputWithError
          className={styles.input}
          value={email}
          changeHandler={(value) => setEmail(value)}
          placeholder="Email"
          error={errors.email}
        />
        <InputWithError
          className={styles.input}
          type="password"
          value={password}
          changeHandler={(value) => setPassword(value)}
          placeholder="Password"
          error={errors.password}
        />
        <div className={styles.checkboxAndLinkWrapper}>
          <div className={styles.checkbox}>
            <input
              type="checkbox"
              id="isRemembered"
              checked={isRemembered}
              onChange={() => setIsRemembered((prevValue) => !prevValue)}
            />
            <label htmlFor="isRemembered">Remember me</label>
          </div>
          <Link className={styles.link} to="/password-reset">
            Forgot password?
          </Link>
        </div>
        <button className={`${styles.button} ${styles.buttonGreen}`}>
          LOG IN
        </button>
      </form>
      <button
        className={`${styles.button} ${styles.buttonBlue}`}
        onClick={() => history.push("/signup")}
      >
        DONT HAVE AN ACCOUNT? - SIGN UP
      </button>
    </div>
  );
};

export default connect(null, {
  logIn: actions.logIn,
})(Login);
