import firestore from "../../firestore";

export const getBrands = async (searchId: string) => {
  const doc = await firestore()
    .collection("brands")
    .doc(searchId)
    .get();
  let brands: Store.Brands = [];
  if (doc.exists) {
    const data = doc.data();
    if (data) {
      const mainBrand = {
        id: data.name || "mainBrand",
        name: data.displayName || "YOUR BRAND",
        role: "main"
      };
      const competitorBrands = data.index.slice(0, 5).map((brandId: string, i: number) => {
        return {
          id: brandId || "competitorBrand" + i,
          name: data.yougov?.[brandId]?.name || data.meltwater?.[brandId]?.base || "YOUR COMPETITOR BRAND " + i,
          role: "competitor"
        };
      });
      brands = [mainBrand, ...competitorBrands];
    }
  }
  return brands;
};
