import axios from "../../axios";

const urlInsertion = process.env.REACT_APP_ENV;

export const deleteUser = (userId: string) => axios.delete(`users-${urlInsertion}-api/${userId}`);

export const postCompanySubscriptionPlanUpgrade = (userId: string, subscriptionPlanId: string) =>
  axios.post(`email-service-${urlInsertion}-api/upgrade-request-email`, { userId, subscriptionPlanId });