import actionTypes from "../actionTypes";
import firebase from "firebase/app";
import "firebase/firestore";
// import { formatDate2, getDateWithMonthOffset, normalizeNumber } from "../../utils";
// import Joi from "@hapi/joi";
// import { initialState } from "./reducer";

// MUTATIONS
export const setWidget = <T extends keyof Store.Widgets>(widgetName: T, widgetData: Store.Widgets[T]) => {
  return {
    type: actionTypes.SET_WIDGET,
    widgetName,
    widgetData,
  };
};

// ACTIONS
export const updateSentimentDriversWidget = (
  searchId: string,
  dateRange: keyof SentimentDrivers.Data,
  brandId: string,
  type: keyof SentimentDrivers.BrandValue,
  data: SentimentDrivers.Value[]
): Function => {
  return async (dispatch: Function, getState: Function) => {
    const { widgets } = getState() as Store.State;
    const sentimentDriversData = { ...widgets.sentimentDrivers };
    sentimentDriversData[dateRange]!.values[brandId]![type] = data;
    dispatch(setWidget("sentimentDrivers", sentimentDriversData));
    const db = firebase.firestore();
    await db
      .collection("result-trending-words")
      .doc(searchId)
      .update({
        [`${dateRange}.values.${brandId}.${type}`]: data,
      });
  };
};
