import React, { CSSProperties } from "react";
import classes from "./ViewTitle.module.scss";
// import { Image } from "../../index";

interface Props {
  className?: string;
  style?: CSSProperties;
  title?: string;
  titleColor?: string;
  question?: string;
  questionColor?: string;
  // clickBackButtonHandler?: () => void;
}
  
export const ViewTitle = ({
  className = "",
  style,
  title = "",
  titleColor = "#efe9ea",
  question = "",
  questionColor = "#cece84",
  // clickBackButtonHandler
}: Props) => {
  return (
    <div className={`${classes.viewTitle} ${className}`} style={style}>
      {/* {clickBackButtonHandler && (
        <button className={classes.backButton} style={style} onClick={clickBackButtonHandler}>
          <Image style={{ transform: "rotate(90deg)" }} name="arrowDownIcon" />
        </button>
      )} */}
      <div style={{ marginRight: 8, color: titleColor, textTransform: "uppercase" }}>{title}</div>
      <div style={{ color: questionColor }}>{question}</div>
    </div>
  );
};
