import React, { useState, useRef, useEffect, CSSProperties, Ref } from "react";
import styles from "./Select.module.scss";
import { Chevron } from "../../../assets/icons";
import { useOutsideClickHandler } from "../../../hooks";

interface Props {
  inputRef?: Ref<HTMLDivElement>;
  className?: string;
  style?: CSSProperties;
  options?: readonly { label: string; value: string }[];
  value?: string;
  changeHandler?: (value: string) => void;
  placeholder?: string;
  isEnabled?: boolean;
}

export const Select = ({
  inputRef,
  className = "",
  style,
  options = [],
  value = "",
  changeHandler = () => "",
  placeholder = "Select here...",
  isEnabled = true,
}: Props) => {
  const [openingDirection, setOpeningDirection] = useState<"up" | "down">(
    "down"
  );
  const [isOpen, setIsOpen] = useState(false);
  const [filter, setFilter] = useState("");

  const selectedOption = options.find((option) => option.value === value);

  const filteredOptions = options.filter((option) =>
    option.label.toLowerCase().includes(filter.trim().toLowerCase())
  );

  const selectRef = useRef<HTMLDivElement>(null);

  useOutsideClickHandler(selectRef, () => {
    setIsOpen(false);
  });

  useEffect(() => {
    if (!isOpen) setFilter("");
  }, [isOpen]);

  useEffect(() => {
    const _setOpeningDirection = () => {
      const clientHeight = document.documentElement.clientHeight;
      const selectTop = selectRef.current?.getBoundingClientRect().top!;
      setOpeningDirection(selectTop > clientHeight / 2 ? "up" : "down");
    };
    _setOpeningDirection();
    window.addEventListener("resize", _setOpeningDirection);
    return () => {
      window.removeEventListener("resize", _setOpeningDirection);
    };
  }, []);

  return (
    <div
      className={`${styles.select} ${className}`}
      style={style}
      ref={selectRef}
    >
      <div
        ref={inputRef}
        className={styles.input}
        style={isEnabled ? undefined : { cursor: "not-allowed" }}
        onClick={
          isEnabled ? () => setIsOpen((prevIsOpen) => !prevIsOpen) : undefined
        }
      >
        {selectedOption ? (
          <div className={styles.label} title={selectedOption.label}>
            {selectedOption.label}
          </div>
        ) : (
          <div className={styles.placeholder} title={placeholder}>
            {placeholder}
          </div>
        )}
        <div className={styles.arrowIconWrapper}>
          <Chevron
            style={{ ...(isOpen && { transform: "rotate(180deg)" }) }}
            size={16}
          />
        </div>
      </div>
      {isOpen && (
        <div
          className={`${styles.popup} ${
            styles[openingDirection === "up" ? "popupUp" : "popupDown"]
          }`}
        >
          {options.length > 5 && (
            <input
              className={styles.filterInput}
              value={filter}
              onChange={(e) => setFilter(e.target.value)}
              placeholder="Type to filter"
            />
          )}
          {filteredOptions.length ? (
            <div className={styles.options}>
              {filteredOptions.map((option, i) => (
                <div
                  className={styles.option}
                  onClick={() => {
                    changeHandler(option.value);
                    setIsOpen(false);
                  }}
                  title={option.label}
                  key={i}
                >
                  {option.label}
                </div>
              ))}
            </div>
          ) : (
            <div className={styles.noOptions}>No results found</div>
          )}
        </div>
      )}
    </div>
  );
};
