  return (
    <div className={styles.termsAndConditionsOfUse}>
      <div className={styles.content}>
        <div className={styles.title}>Terms and conditions of use</div>
        <div className={styles.text}>
          My Telescope and the Competitive Compass is a service provided by
          Graviz Pirenne Labs AB, with VAT registration number SE-559014-7483.
          We have the right to terminate any Services or accounts through which
          any IP (including My Telescope Competitive Compass) has been
          infringed, or where use of the Services (including any content you
          upload or distribute) breaches any applicable law.
          <br />
          <br />
          For the moment, My Telescope is running a free trial of her Digital
          Platform and its functionalities. This free trial is provided to give
          you access to the system at no charge, so that you can become more
          familiar with its features and discover first-hand the Digital
          Platform and preview all it has to offer for you and your business.
        </div>
      </div>
    </div>
  );
