import {
  createEntityAdapter,
  createAsyncThunk,
  createSlice,
} from "@reduxjs/toolkit";
import * as Api from "./campaignsApi";

export interface Campaign {
  id: string;
  name: string;
  startDate: string;
  endDate: string;
  createdAt: string;
}

const campaignsAdapter = createEntityAdapter<Campaign>({
  sortComparer: (a, b) => a.createdAt.localeCompare(b.createdAt),
});

const initialState = campaignsAdapter.getInitialState();

export const fetchCampaigns = createAsyncThunk<
  Campaign[],
  void,
  { state: RootState }
>("campaigns/fetchCampaigns", async (_, { getState }) => {
  const searchId = getState().current.searchId;
  const campaigns = await Api.getCampaigns(searchId);
  return campaigns;
});

export const addCampaign = createAsyncThunk<
  Campaign,
  Omit<Campaign, "id" | "createdAt">,
  { state: RootState }
>("campaigns/addCampaign", async (payload, { getState }) => {
  const searchId = getState().current.searchId;
  const { id, createdAt } = await Api.addCampaign({ searchId, ...payload });
  return {
    id,
    ...payload,
    createdAt,
  };
});

export const updateCampaign = createAsyncThunk<Campaign, Campaign>(
  "campaigns/updateCampaign",
  async (payload) => {
    await Api.updateCampaign(payload);
    return payload;
  }
);

export const removeCampaign = createAsyncThunk<Campaign["id"], Campaign["id"]>(
  "campaigns/removeCampaign",
  async (payload) => {
    await Api.deleteCampaign(payload);
    return payload;
  }
);

const campaignsSlice = createSlice({
  name: "campaigns",
  initialState,
  reducers: {},
  extraReducers: (builder) => {
    builder.addCase(fetchCampaigns.fulfilled, campaignsAdapter.setAll);
    builder.addCase(addCampaign.fulfilled, campaignsAdapter.upsertOne);
    builder.addCase(updateCampaign.fulfilled, campaignsAdapter.upsertOne);
    builder.addCase(removeCampaign.fulfilled, campaignsAdapter.removeOne);
  },
});

export const {
  selectAll: selectAllCampaigns,
} = campaignsAdapter.getSelectors<RootState>(({ campaigns }) => campaigns);

export default campaignsSlice.reducer;
